[data-topnav=job-detail] {
  // this is used for styling both the output and the error text for a job
  div.result-text {
    .overflow-shadow;
    background-color: inherit;
    font-size: 90%;
    max-height: 15em;
    border: 1px solid @border-color;
    border-radius: 2px;
    margin-bottom: 1em;
    padding: 4px;
  }

  div.errors {
    color: red;
  }

  .myMarker {
    position:absolute;
    background: rgba(100,200,100,0.5);
    z-index:20
  }

  div.output {
  }

  #job-progress {
    height: 15em;
    overflow-y: scroll;
  }

  .job-progress {
    // Make job progress text easy to select
    cursor: text;
    width: 100%;
    border-radius: 2px;
    border: 1px solid @border-color;
  }
  .job-progress a {
    cursor: pointer;
  }

  .job-progress-section .close-output {
    float: right;
  }

  textarea.terminal {
    background-color: black;
    color: #33ff66;
    font-family: monospace;
  }

  // Order details shows jobs as well.
  // Indent child jobs
  li.list-group-item.child-job {
    margin-left: 4em;
  }
}
