// CloudBolt logo loading indicator with pulsing animation
.spinner {
  animation: pulse 1.3s infinite;
  // Image should have alpha transparency; SVG will work here too
  background: url('../logos/cloudbolt-lines-logo-blue.png') no-repeat 0 0;
  height:122px;
  width:122px;
  display: block;

  // Center in the container
  position: relative;
  margin: 0 auto;
  vertical-align: middle;
}

// Pulse curve is on more of a basketball rhythm than a perfect sine.
// To keep this more pleasing and subtle, opacity is never on either extreme.
@keyframes pulse {
  0% { opacity: 0.7; }
  25% { opacity: 0.5; }
  50% { opacity: 0.2; }
  75% { opacity: 0.5; }
  100% { opacity: 0.7; }
}


// a ring-shaped spinner with each quarter alternately colored
.spinner-ring {
  @primary-color:   mix(@header-bg, @body-bg, 90%);
  @secondary-color: mix(@header-bg, @body-bg, 10%);

  @revolution-time: 2s;
  @diameter: 60px;
  @thickness: 8px;

  margin: 0 auto;
  position: relative;

  width: @diameter;
  height: @diameter;

  border-width: @thickness;
  border-style: solid;
  border-color: @primary-color @secondary-color;
  border-radius: 100%;

  -webkit-animation: rotation @revolution-time infinite linear;
     -moz-animation: rotation @revolution-time infinite linear;
       -o-animation: rotation @revolution-time infinite linear;
          animation: rotation @revolution-time infinite linear;
}

@-webkit-keyframes rotation {
  from {-webkit-transform: rotate(0deg);}
    to {-webkit-transform: rotate(359deg);}
}

@-moz-keyframes rotation {
  from {-moz-transform: rotate(0deg);}
    to {-moz-transform: rotate(359deg);}
}

@-o-keyframes rotation {
  from {-o-transform: rotate(0deg);}
    to {-o-transform: rotate(359deg);}
}

@keyframes rotation {
  from {transform: rotate(0deg);}
    to {transform: rotate(359deg);}
}
