/*
 * Style clickable lists or tables
 *
 * See also js/c2/jquery.clickable.js
 */
.clickable {
  tr, li {
    cursor: pointer;
  }

  // Row or list item is selected
  // the extra td is a hack to increase selector specificity; otherwise, the
  // datatable style for sorted colums takes precedence.
  li.selected,
  tr.selected,
  tr.selected td {
    background-color: @highlight-bg !important;
  }

  // Slightly darker hover color when row is already highlighted
  li.selected:hover,
  tr.selected:hover td {
    background-color: darken(@highlight-bg, 8%) !important;
  }
}

.not-clickable {
  cursor: default;
}
