// This catalog.less file is referenced from the catalog page and the reports page.

// This special case enables us to keep the blueprint order page compact.
#content.catalog.compact-layout {
  // Keep things from getting spread out too much on large screens.
  // Width chosen for the best layout proportions with SI form fields.
  max-width: 1300px !important;
  margin: 0 auto !important;

  &.more-compact {
    max-width: 1000px !important;
  }
}

#catalog-visual-view {
  .btn-toolbar .selectize-control {
    display: inline-block;
    min-width: 15em; // wider than the minimum
  }
}

// Constrain blueprint icons to a reasonable size
img.blueprint {
  max-height: 200px;

  // Tabular list view
  &.thumbnail {
    max-width: 8em;
    max-height: 5em;
  }
}

// Dimmed content positioned somewhere on a BP card
.blueprint-card-addon {
  position: absolute;
  top: 0;

  color: lighten(@text-color, 50%);
  transition: color 100ms;
  padding: 8px;

  &:hover {
    color: @text-color;
    cursor: pointer;
  }
}

.blueprint-card,
.resource-card {
  .panel;
  .panel-default;

  // Support absolutely positioned elements
  position: relative;

  background-color: #fff;

  &.orderable:hover {
    cursor: pointer;
    border-color: darken(@table-border-color, 15%);
    .box-shadow(inset 0px 0px 30px 0px rgba(210, 210, 210, 1));
  }

  // Let the inset shadow show through
  .panel-body,
  .panel-footer {
    background-color: transparent;
  }

  .panel-footer {
    // Remove Bootstrap's default border
    border: none;

    // Blueprint name
    h5 {
      font-weight: normal;
      // Card's padding takes care of vertical space but we need a little
      // side margin to avoid long names touching card edges
      margin: 0 4px;
    }

    // Cog icon
    a.import-link,
    a.manage-link {
      .blueprint-card-addon;
      right: 0;
      font-size: @font-size-h3;
    }

    // Tags icon with Labels in a tooltip
    .fa-tags {
      .blueprint-card-addon;
      left: 0;
    }
  }

  // Large cards on Catalog list and job details
  &.card-size-large {
    // Large cards are fixed-size blocks with floating layout.  We used to
    // use masonry layout but it had some quirks that occasionally resulted in
    // ugly/non-sensical layouts.
    display: block;
    float: left;

    width: 260px;
    height: 220px;
    margin: 12px 8px;
    text-align: center;

    .panel-body {
      height: 170px;

      img {
        // Constrain tall images to a reasonable height. Wide images are
        // constrained by width of columns.
        max-height: 140px;
        max-width: 225px;

        // centered horizontally
        margin: 0 auto;
      }
    }

    .panel-footer {
      // Force the footer to be at most tall enough for 2 lines of text
      height: 50px;
      padding: 0;

      a.manage-link {
        font-size: @font-size-h3;
      }
    }
  }

  // Mini cards on resource lists, deployed resource Overview tab
  &.card-size-mini {
    // Very long names wrap to 2 lines and card still looks good
    height: 60px;
    margin: 0;
    padding: 0 4px;
    text-align: left;

    // Scrollbars instead of letting content spill out
    overflow: auto;

    .panel-footer {
      .blueprint-image {
        display: inline-block;
        text-align: center;
        width: 40px;

        img {
          max-height: 40px;
          max-width: 40px;
        }
      }

      // Blueprint name
      h5 {
        display: inline-block;
        margin: 0 8px;
      }

      // Cog
      a.manage-link {
        font-size: @font-size-h4;
        top: 4px;
      }
    }
  }

  // Historical blueprints
  &.historical {
    h5,
    img {
      opacity: 0.5;
    }
  }

  // Make it clear when blueprints exist on a remote CB server
  &.remote {
    h5,
    img {
      opacity: 0.5;
    }

    img {
      -webkit-filter: grayscale(1); /* Google Chrome, Safari 6+ & Opera 15+ */
      filter: grayscale(1); /* Microsoft Edge and Firefox 35+ */
    }

    background-color: #f1f1f1;
    // a transparent shade of CB blue
    border: 1px dashed #d6d6d6;
  }

  &.remote:hover {
    h5,
    img {
      opacity: 1;
    }

    img {
      -webkit-filter: grayscale(0); /* Google Chrome, Safari 6+ & Opera 15+ */
      filter: grayscale(0); /* Microsoft Edge and Firefox 35+ */
    }
  }
}

// Order details page

#blueprint-order-view {
  .blueprint-overview {
    .btn-toolbar {
      float: right;

      // Move .btn-toolbar to the left on small screens
      @media (max-width: 600px) {
        float: none;
      }
    }

    // Wraps both the image and the description text
    .bp-description-wrapper {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;

      // Allow wrapping so that the description text gets pushed to a new line on small screens
      @media (max-width: 769px) {
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;

        img.blueprint {
          margin-bottom: 15px;
        }
      }

      img.blueprint {
        margin-right: 2em;
        max-height: 100px;
      }

      .description {
        margin-right: 2em;
        // Tell the description text to take up all of the space its parent flex container provides
        -webkit-box-flex: 1;
        -ms-flex-positive: 1;
        flex-grow: 1;
      }
    }
  }
}

#blueprint-order-form {
  // Undo the negative margins on .form-group in this view; it causes more
  // trouble than it's worth.
  .form-group {
    margin-left: 0;
    margin-right: 0;
    .col-lg-9.order-form__section,
    .controls {
      padding-left: 8px;
    }
    // Prevent form fields from getting too wide
    .form-control,
    .selectize-control {
      max-width: 600px !important;
    }
  }
}

/*
 * Order form cost preview
 *
 * Initially this is hidden and scaled down. When shown, a nice scaling/fading in transition
 * animates it.
 */
#cost-preview-wrap {
  opacity: 0;
  visibility: hidden;
  .transform-origin(top);
  .transform(scaleY(0.75));
  .transition(all @out-duration @ease-in-quad);

  &.show {
    display: block;
    opacity: 1;
    visibility: visible;
    .transform(scaleY(1));
    // Bring into view slowly enough that it's pleasing instead of jarring
    .transition(all 500ms @ease-out-quad);
  }
}

// This wraps the global non-SI form fields
#content.more-compact #order-form-wrap {
  // Align label and fields with the SI form fields below
  margin-left: 10px;
}

table#service-items {
  .item-info {
    width: 30%;
  }
  .item-envs {
    width: 25%;
  }
  .item-params {
    width: 45%;
  }

  .runtime-inputs {
    min-width: 440px;
    table {
      th {
        vertical-align: top;

        label {
          min-width: 120px;
          margin-top: 0.5em;
        }
      }
      td {
        input,
        select,
        textarea {
          width: 320px;
          margin-bottom: 5px;
        }
      }
    }
  }
}

