// Diagrams
@keyframes server_pulse_animation {
  0% {
    transform: scale(1);
    opacity: 0.2;
  }
  50% {
    transform: scale(1.05);
    opacity: 0.8;
  }
  100% {
    transform: scale(1);
    opacity: 0.2;
  }
}

.resource-diagram {
  overflow: auto;

  .tier {
    padding: 8px;
    margin-bottom: 12px;
    border: 1px solid @border-color;
    border-radius: 8px;
    // background-color: #eee;
    clear: both;

    img.env-tech {
      float: left;
      margin-left: 32px;
      margin-right: 16px;
    }
  }

  .env-servers {
    float: left;
    text-align: center;
    display: table;
    position: relative;
    // align with env's tech icon
    top: -5px;

    img {
      display: block;
      width: 50px;

      &.pulsate {
        animation-name: server_pulse_animation;
        animation-duration: 1.5s;
        transform-origin: 70% 70%;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
      }
    }

    .server-count {
      position: relative;
      // move closer to bottom of server image
      top: -5px;
    }
  }
}

.reports-list {
  .tile--logo-wrapper {
    text-align: center;
  }
  .tile--logo.report-thumbnail {
    width: 100%;
    max-height: none;
    max-width: none;
  }
  .report-name {
