/*
 * Accessibility
 *
 * Bootstrap also provides some styles, such as .sr-only
 */


/*
 * For any link that must be visible to screenreaders and when focused by
 * tabbing to it. I.e. the accessibility link to skip navigation.
 */
a.show-on-focus {
  position: absolute;
  top: 10px;
  left: -1000px;
  z-index: 2000;

  // Make it easy to spot when focused
  padding: 4px;
  color: #000000;
  background: #ffffff;
  text-decoration: none;

  &:focus,
  &:active {
    left: 10px;
    outline: none;
  }
}
