// Icons
// --------------------------------------------------
// There are various sources of icons: images (e.g. Silk), glyphs from
// font-based icon sets such as Font Awesome and Bootstrap, and custom-made
// images (e.g. our resource technology icons).  To decouple the semantics of
// an icon from its implementation, we define semantic icon classes like
// `.icon-{class}` that map to a font-based or image-based icon under the hood.


// Font Awesome
// --------------------------------------------------

@fa-less-path: "../less/font-awesome/";
@import "@{fa-less-path}fontawesome.less";
@import "@{fa-less-path}brands.less";
@import "@{fa-less-path}light.less";
@import "@{fa-less-path}regular.less";
@import "@{fa-less-path}solid.less";

// Below is not an ideal way of integrating FA. Ideal would be a simple
// import of font-awesome.less to get all LESS variables and declarations, and
// simply using them in our LESS like this:
//   .icon-delete { .fa; .fa-times; }
//
// But FA does not provide mixins that can be used in this way.  There's
// an open RFE here: https://github.com/FortAwesome/Font-Awesome/issues/2279
// So we have these custom mixins that ease LESS development for FA icons.

// Alias .fa to the .fa-icon mixin provided by Font Awesome
.fa() {
  .fa-icon;

  // FA icons need a little help getting centered, e.g. check icons in tables
  &.text-center {
    display: block;
  }
}

// --- Fix rules clobbered by the above, which comes after the FA CSS imported
// via `all-font-faces.css`.
.fa-stack-1x {
  line-height: inherit;
}
.fa-stack-2x {
  font-size: 2em;
}
// --- end fix

// Mixin makes it easy to use a specific FA icon by name:
// .fa-glyph(plus) creates a :before rule using @fa-var-plus
.fa-glyph(@name) {
  @varname: "fa-var-@{name}";
  &:before { content: @@varname; }
}

// Mixins for Font Awesome glyphicons
.fa-icon (@name; @glyph) {
  .icon-@{name} {
    .fas;
    .fa-glyph(@glyph);
  }
}
// Mixins for Font Awesome glyphicons
.fa-icon-b (@name; @glyph) {
  .icon-@{name} {
    .fab;
    .fa-glyph(@glyph);
  }
}


// Bootstrap
// --------------------------------------------------
// I (TH) was unable to write similar mixin for Bootstrap's glyphicons. Because
// they only declare the glyph *rules*, there is no easy way to wrap them in a
// mixin that can be called from here. It's also arguably simpler to just use the
// classes, which is what we do below.  It's not consistent, but still achieves
// the goal of maintainable icon mappings.


// Image-based icons
// --------------------------------------------------
// These icons require the 'icon' class to set up layout of the element.
// One mixin enables different sizes to be easily defined.

.icon {
  display: inline-block;
  vertical-align: middle;

  // for any images that aren't quite square (even though they should be)...
  background-position: center center;
  background-repeat: no-repeat;

  // hide text --- icon is replacing it
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;

  /* Put some space between icon and the next visible sibling element.  We do
   * this instead of adding right margin to all icons, which would break
   * alignment of right-aligned icons.
   */
  + * {
    margin-left: 0.25em;
  }
}

// Icons used in headings don't have a box to be middle-aligned within (like
// icons used in table cells) so we provide alignment with the baseline of the
// text.
h1, h2 {
  .icon {
    vertical-align: baseline;
  }
}

/* Dimensions */
.icon-16  { width: 16px;  height: 16px;  }
.icon-30  { width: 30px !important;  height: 30px !important;  }
.icon-32  { width: 32px !important;  height: 32px !important;  }
.icon-60  { width: 60px !important;  } // specific to servicenow img
.icon-64  { width: 64px !important;  height: 64px !important;  }
.icon-128 { width: 128px !important; height: 128px !important; }

// Mixins for image-based icons
.bg-img-icon (@filename) {
  @base-url: "../img/icons/";
  background-image: url("@{base-url}@{filename}");
}
.img-icon (@name; @filename; @size: 16) when (@size = 16) {
  .icon-@{name} { // no size class because we default to 16
    .icon;
    // Not sure why .icon-@{size} does not work here; it would avoid having a
    // 2nd mixin for other sizes.
    .icon-16;
    .bg-img-icon(@filename);
  }
}
.img-icon (@name; @filename; @size: 16) when not (@size = 16) {
  .icon-@{name}.icon-@{size} {
    .icon;
    .bg-img-icon(@filename);
  }
}
.png-icon-set (@name; @filename-sans-extension) {
  .img-icon(@name; "@{filename-sans-extension}.svg"; 16);
  .img-icon(@name; "@{filename-sans-extension}.svg"; 30);
  .img-icon(@name; "@{filename-sans-extension}.svg"; 32);
  .img-icon(@name; "@{filename-sans-extension}.svg"; 64);
}


