// Gray for disabled buttons (Bootstrap also applies 65% opacity to these)
.btn.disabled .caret,
.btn.disabled [class^="icon-"] {
  color: #cbcbcb;
}

// Misc
// --------------------------------------------------

// Icon links
a[class^="icon-"]:not(.btn) {
  // Larger click target
  padding: 3px;
}

a.icon:hover,
a.fa,
a.glyphicon,
a[class^="icon-"]:hover {
  text-decoration: none;
}


/* buttons with just an icon inside should not have margins or else it'll be uncentered */
.btn .icon:only-child {
  margin: 0;
}

/* secondary button icons are on the right side of the text */
.icon.ui-button-icon-secondary {
  margin-right: 0;
}

td > .icon-check {
  margin: auto;
}

/* Hierarchical representation for some list-view links */
.uplink {
  display: inline-block;
  background: url(../img/icons/grey-arrow-right-down.png) no-repeat center right;
  padding-right: 18px;
  min-height: 18px;

  font-size: .90em;
  font-weight: normal;

  & + h1 {
    margin-top: 0;
  }
}

/* ajax/wait loading icon */
.loading {
  height: 20px;
  width: 20px;
  background: transparent url(../img/loader-sm-333.gif) no-repeat center center;
  display: block;
}

// file upload widget
.upload {

  .image-preview {
    margin: 1em 0;
  }
}
.thumbnail {
  max-width: 100px;
}

// glyphicon info icon
.infotip {
  color: #ccc;
  .glyphicon;
  .glyphicon-info-sign;

  // apply the equivalent of .no-tooltip-affordance
  border-bottom: none !important;
}

// Color specific button icons when they are in the depressed state
// (e.g. batch user permissions dialog on Groups > Users tab)
.btn.active .active-color-success,
.btn.active .icon-delete { color: darken(@alert-danger-bg, 40%); }
.btn.active .active-color-error,
.btn.active .icon-checkmark { color: darken(@alert-success-bg, 40%); }
.btn.active .active-color-info { color: darken(@alert-info-bg, 40%); }
