// Cards for servers, resources, blueprints, actions, and other objects
// --------------------------------------------------

@card-bg-color: #eee;
@card-border-color: darken(@card-bg-color, 5%);

.card-box {
  background-color: @card-bg-color;
  border: 1px solid @card-border-color;
  border-radius: @border-radius-base;
  padding: @padding-small-vertical @padding-small-horizontal;
}

.card-box-hover {
  background-color: darken(@card-bg-color, 5%);
  border-color: darken(@card-border-color, 5%);
}

.card {
    a > & {
        .card-box;
    }

    a:hover > & {
        .card-box-hover;
    }
}

