// Admin-specific layout tweaks
//----------------------------------------

body[data-topnav=admin-index] {
  h1 {
    text-align: center;
    font-size: 1.4rem;
    font-weight: 300;
  }
  h3 {
    font-size: 20px;
  }
  .spotlight-search-bar {
    .clearable-wrapper {
      width: 100%;
      .input-group {
        width: 100%;
        .glyphicon-search {
          position: absolute;
          right: 1rem;
          top: .55rem;
          z-index: 5;
          color: #555555;
        }
      }
    }
  }
  .spotlight-results > ol {
    margin-bottom: 0;
  }
  .panel {
    text-align: left;

    .panel-heading {
        color: @blue-3;
        background-color: #fff;
        margin: 0 1rem;
        padding: 0;
        .revealable {
          opacity: 1;
          top: 3px
        }
    }

    ul.list-styled {
      margin: .5rem 0;
      li {
        list-style: disc;
        a {
          padding-left: 0 !important;
        }
      }
    }

    ul:not(.recent-servers) li {
      font-size: 120%;
      /* slight gap between links makes mis-clicks less likely */
      padding: .2em 0;

      a {
        /* generous click target */
        padding: .1em .5em;
        width: 100%;
        display: inline-block;
      }
    }

    ul.recent-servers {
      padding-left: 0;
      li {
        a {
          margin: 4px;
        }
      }
    }
  }
}
