#groupTreeScroll {
    overflow-x: auto;
    background-color: #ffffff;
    border: 1px solid #ddd;
    border-radius: 4px;
}

#groupTreeContainer {
    margin: 0 auto 0 15em;
}

#groupTree {
    border: none;
    min-width: 50em;
    background-color: transparent;

    th {
        background-color: #f7f7f7;
        padding-bottom: 1em;
        padding-top: 1em;
    }
    tbody tr:first-of-type td {
        padding-top: 20px;
    }
    td {
        background-color: transparent;
        padding-top: .6em; padding-right: 0; padding-bottom: .6em;
        font-size: 14px;
        line-height: 20px;
    }
    th.name,
    td.first {
        box-sizing: border-box;
        position: absolute;
        min-width: 15em;
        max-width: 15em;
        margin-left: -15em;
        padding-left: 30px;
        overflow: hidden;
        white-space: nowrap;
        background-color: @default-content-bg-color; /* Required for scrolling */
                                                     /* Overridden in group list page */
        z-index: 10;
        text-overflow: ellipsis;
    }
    span.maximum {
        color: #aaa;
    }
}

#groupTree tr.collapsed td { animation-name: show; animation-duration: .5s; animation-delay: 0s; }
#groupTree tr.parent.collapsed td { animation-name: none; }
@keyframes show { from { opacity: 0; } }

.ui-helper-hidden  { transition: 2s linear all; }
@keyframes hide { to { opacity: 0; } }


