/**
 * Global Haystack search menu and dropdown
 */

[data-topnav=search] {
  .dropdown-menu {
    width: 100vw;
    min-height: 50vh;
  }
  .dropdown-search {
    margin-top: 2rem;
    width: 100%;
  }
  form#navbar-search {
    display: flex;
    justify-content: center;
    > .form-group {
      // zero margins to keep header intact
      margin: 0;
      min-width: 33.33%;
    }
    .clearable-wrapper,
    #search-input {
      width: 100%;
    }
    .clearable-wrapper {
      .clearable-trigger {
        top: .4rem!important;
      }
    }
  }
  #search-results {
    padding: 1em;
    text-align: center;
    ol > li:hover {
      cursor: pointer;
    }
    .post_result {
      padding: 3px 8px;
      a {
        padding: 3px 8px;
        border: 1px solid transparent;
        font-size: 1rem;
        line-height: 2rem;
        i, span {
          display: none;
        }
        &:hover,
        &:focus {
          color: #000;
          text-decoration: underline;
        }
      }
    }
    .search-result-pager {
      padding: 8px;
      a {
        padding: 8px;
        cursor: pointer;
      }
    }
  }
}


/**
 * Spotlight search feature (on Admin page)
 *
 * As user types, matching elements are highlighted in place.
 */

// Container
.spotlight-search-bar {
  width: 25em;
  margin: 2em auto;
  // Required for the dropdown to be properly positioned
  position: relative;
}

// Highlight style for elements matching user's search
.spotlight {
  background-color: @highlight-bg;
  border-radius: 2px;
  // Dark shadow outline
  .box-shadow(0 4px 10px -4px rgba(0,0,0,0.3));
}

.spotlight-results {
  // Positioned below the search input field
  position: absolute;
  padding: 1em;
  background-color: #fff;
  border: 1px solid rgba(0,0,0,0.1);
  border-radius: 4px;
  .box-shadow(0 7px 25px -11px rgba(0,0,0,0.5));
  // Below topnav dropdown menus
  z-index: 500;

  // Initially this is hidden and scaled down like top navbar dropdowns. On
  // hover, a nice scaling/fading in transition animates them.
  opacity: 0;
  visibility: hidden;
  .transform-origin(top);
  .transform(scaleY(0.75));
  .transition(all @out-duration @ease-in-quad);

  &.active {
    display: block;
    opacity: 1;
    visibility: visible;
    .transform(scaleY(1));
    .transition(all @in-duration @ease-out-quad);
  }

  li {
    margin: .25em 0;
  }

  a {
    padding: .25em .5em;
    border: 1px solid transparent;
    display: inline-block;
    width: 100%;

    &:hover,
    &:focus {
      background-color: @table-bg-hover;
      border-radius: @border-radius-small;
      border: 1px solid #ddd;
      text-decoration: none;
      outline: none;
    }
  }
}
