.catalog-list__blueprint {
    background: #fff;
    border-radius: 5px;
    margin-bottom: .5rem;
    height: 6rem;
    cursor: pointer;
    position: relative;
    transition: box-shadow .3s linear;
    overflow: hidden;
    box-shadow: 0px 0px 2px 1px rgba(87,87,87,0.5), inset 0 0 2px 1.5px rgba(87,87,87,0);
    a {
        display: flex;
        flex-basis: 95%;
        height: 100%;
        align-items: center;
        padding: 0 1rem;
        &:hover {
            text-decoration: none;
            color: #000;
        }
    }
    .favorite::after {
        content: '';
        width: 3rem;
        height: 3rem;
        transform: rotate(45deg);
        position: absolute;
        top: -25px;
        left: -25px;
        background: #04D300;
    }
    .ls-item {
        margin: 0 .5rem;
        &.blueprint__name {
            flex-basis: 20%;
            height: 100%;
            display: flex;
            align-items: center;
        }
        &.catalog-list__logo {
            flex-basis: 8%;
            display: flex;
            justify-content: center;
            align-items: center;
            height: 100%;
            min-width: 4rem;
            .tile--logo {
                display: block;
                width: 100%;
                margin: 0;
            }
        }
        &.catalog-list__config {
            background: #FF7A00;;
            text-align: center;
            color: #fff;
            flex-basis: 15%;
            line-height: 1.25rem;
            height: 100%;
            .alert--emphasis {
                text-decoration: none;
                display: flex;
                flex-direction: column;
                justify-content: center;
                height: 100%;
            }
        }
        &.catalog-list__details {
            flex-basis: 15%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            height: 100%;
            p {
                margin: 0;
                display: flex;
                justify-content: space-between;
                &.light.small {
                    color: #9D9D9D;
                    font-size: .7rem;
                    span.dark {
                        color: #575757;
                        font-weight: 700;
                    }
                }
            }
        }
        &.catalog-list__description {
            flex-basis: 45%;
            height: 100%;
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            max-width: 18rem;
            overflow-y: scroll;
            @media (min-width: 1200px) and (max-width: 1400px) {
                max-width: 20rem;
            }
            @media (min-width: 1401px) {
                max-width: 28rem;
            }
            p {
                width: 100%;
            }
        }
        &.catalog-list__icon {
            display: flex;
            justify-content: center;
            align-items: center;
            .catalog-icon-gear {
                position: absolute;
                top: 2.4rem;
            }
        }
    }
    &:hover  {
        // border: 1px solid #575757;
        box-shadow: 0px 0px 2px 1px rgba(87,87,87,0), inset 0 0 2px 1.5px rgba(87,87,87,0.5);
    }
    &:active {
        box-shadow: 0px 0px 2px 1px rgba(87,87,87,0), inset 0 0 2px 2px rgba(87,87,87,0.6);
    }
}


