/**
 * Bootstrap collapse component
 */
[data-toggle=collapse] {

  // Let user know they can click on the trigger text
  cursor: pointer;

  // Larger click target, but not indented
  padding: 0.5em 0.5em 0.5em 0;

  // Add an indicator to collapse triggers that contain a glyphicon to act as an
  // affordance to users about this feature.
  .collapsed-indicator {
    .glyphicon;
    .glyphicon-chevron-down; // open
    color: lighten(@text-color, 60%);
    font-size: 90%;
    margin-right: 5px;

    &:hover {
        color: @link-color;
    }
  }

  &.collapsed .collapsed-indicator {
    .glyphicon-chevron-right; // closed
  }
}

// Center these vertically
.panel-heading [data-toggle=collapse] {
    // center in panel-headings
    vertical-align: middle;
    display: inline-block;
}
