
// pre-Bootstrap table stylings
table {
  margin-bottom: @line-height-computed;

  > thead > tr > th {
    font-weight: normal;
  }

  /* Global overrides to Bootstrap's .table style
   */
  &.table {
    > thead > tr > th {
      // Make 1px border beneath table header instead of 2px
      border-bottom-width: 1px;
    }

    > tbody {
      // lighten up the border between rows
      tr > td {
        border-top-color: lighten(@table-border-color, 8%); // #f1f1f1
        word-break: break-word;
      }
    }
  }

  // Bootstrap's sets embedded tables to @bg-color, which is undesirable
  .table &.table {
    background-color: transparent;
  }

  // Special treatment for interactive datatables
  &.dataTable {
    thead th:focus {
      background-color: @table-border-color !important;
    }
  }

  // Column width controls
  th.shrink,
  td.shrink {
    white-space: nowrap;
  }
  th.expand,
  td.expand {
    width: 99%;
  }
  th.shrink .tooltip {
    /* BS tooltips in THs that have .shrink will otherwise cut off after 1
     * line.  See User/Groups tables. */
    white-space: normal;
  }
  th.icon-and-label {
    min-width: 200px;
  }
}

.table-boxed {
  border: 1px solid @table-border-color;
}


/* table.stats is applied in many places: order items, group tree, quota tables, and more.
 * It used to have a blue theme (header bg and border) but is now styled more
 * like datatables and panels.
 */
.stats {
  border-spacing: 0;
  width: 100%;
  border: 1px solid @table-border-color;
  border-radius: @panel-border-radius;
  border-collapse: inherit; // without this corners are not rounded
  // hide square corners covering the table's rounded border
  overflow: hidden;

  > thead th {
    background-color: @panel-default-heading-bg;
    border-bottom: 1px solid @table-border-color;
  }

  > tbody > th {
    font-weight: bold;
  }

  > thead,
  > tbody,
  > tfoot {
    > tr {
      > th,
      > td {
        padding: @table-cell-padding;
        // Top-align for better presentation of tall content such as many
        // server cards in order detail view.
        vertical-align: top;
      }
    }
  }

  > tbody,
  > tfoot {
    > tr {
      > th,
      > td {
        background-color: #fff;
      }
    }
  }

  /* Special header and/or footer row for controls */
  > thead,
  > tfoot {
    > tr.controls > th {
      text-align: center;
      background-color: #fff;
      color: #77787b;
    }
  }

  /* lighter border than default dataTable style; slightly lighter than table.stats border */
  > thead > tr.controls > th {
    border-bottom: 1px solid #ddd;
  }
  > tfoot > tr.controls > th {
    border-top: 1px solid #ddd;
  }

  caption {
    background-color:#7CA0C7;
    color: #fff;
    font-weight: bold;
  }
}


// tables with numeric columns that look better right-justified
table.numeric-stats {
  // This ensures all such numeric tables' number columns are horizontally aligned with all others
  // in a columns. See Admin > Rates > Environments for example.
  width: 100%;

  tbody {
    th {
      font-weight: normal;
      text-align: left;
      white-space: nowrap;
    }
    td {
      font-weight: bold;
      text-align: right;
      white-space: nowrap;
    }
  }
}

// Tables used similarly to <dl> lists, but that lay out better when
// the "term" column width wants to be determined by the longest term
// as opposed to just a relative percentage.
table.definitions {
  th {
    padding-right: 1em;
    vertical-align: top;
    white-space: nowrap;
  }
}


// Tables without Bootstrap's default rule lines
table.unruled {
  td, th {
    border-top: none !important;
  }
}


// Active search term is highlighted near row count info area
.matching-term b {
  color: @blue-3;
}


// In the global help dialog
table.keyboard-shortcuts tbody {
  @keycap-border-color: #bbb;
  @keycap-bg-color: #f3f3f3;

  th {
    font-weight: normal;
    text-align: right;
    white-space: nowrap;
    padding: 10px;
  }

  code {
    padding: 4px 8px;
    background: @keycap-bg-color;
    border: 1px solid @keycap-border-color;
    border-bottom-color: lighten(@keycap-border-color, 20%);
    .box-shadow(inset 0 -2px 0 -1px rgba(0,0,0,0.5));
    font-family: monospace;
  }

  td {
    vertical-align: middle;
  }

}

.dataTables_wrapper {
  .clearfix();
}
