/*
Elements at the bottom of a container which also declare a margin-bottom can
cause a large space to be created at the bottom of that container. These rules
reset the margin of those last children so that just the padding in that
container is responsible for the spacing at the bottom, resulting in a better
look.
*/

th,
td,
.panel,
.panel-body,
.panel-heading,
.modal-body,
.list-group-item,
.tab-pane {
  > :last-child {
    margin-bottom: 0;
    > :last-child {
      margin-bottom: 0;
      > :last-child {
        margin-bottom: 0;
      }
    }
  }

  > pre:first-child {
    margin-top: 0;
  }
}

.custom-control {
  &-label {
    margin-left: 0.5rem;
  }
}

// fix jQuery UI dialog's built-in buttons because the above rule
// was breaking their layout
.ui-dialog .ui-dialog-buttonpane button {
  margin-bottom: 0;
}
