// we've added the ability to specify popover content via an element in the dom
// vs jamming the content into a data-content attribute. We need to hide that
// element.
// This is needed to prevent style clashing with Bootstrap 4 on the dashboard.

.popover {
  max-width: 40em !important;
  .popover-header {
    color: #212529 !important;
    border-bottom: 1px solid #ccced2 !important;
    font-size: 1rem !important;
    padding: 0.5rem 1rem;
    word-wrap: break-word;
  }
  .popover-body {
    padding: 0.5rem 1rem;
    word-wrap: break-word;
  }
  &.fade:not(.show) {
    opacity: 1 !important;
  }
}
.popover-content-el {
  display: none;
}
