/* DataTables customizations
 */

@disabled-color: silver;
@enabled-color: black;

html body .dataTables_wrapper {
  @panel-padding: @line-height-computed / 2;

  .panel-body {
    // frame the table (which isn't wrapped in a panel-body) by darkening the
    // "header" and "footer"
    background-color: darken(@panel-bg, 3%);
    padding: @panel-padding;

    &.dataTables-header {
      padding-bottom: 0;
    }

    & + table.dataTable {
      // Remove border between info areas and column headings
      border-top: none;
    }
  }

  // Enable tooltips on disabled buttons. By default, Bootstrap disables the tooltip
  // as well, which makes it hard to explain to users why an action is not available.
  .btn.disabled {
      pointer-events: auto;
  }

  // Tables with no thead row are missing the border and padding between the
  // header and table contents. The "noHeadings" class is added by our custom init logic.
  &.noHeadings .panel-body.dataTables-header {
    border-bottom: 1px solid @table-border-color;
    // restore the padding removed above
    padding-bottom: @panel-padding;
  }

  .panel-body label {
    // the dataTables_length label has a margin bottom that upsets some spacing
    margin-bottom: 0;
  }

  // Provide scrollbars for wide contents, e.g. more columns than fit on a page
  // layout
  .scroll-wide-content {
    overflow-x: auto;

    // Override 20px bottom margin caused by the insersion of the
    // div.scroll-wide-content
    table.dataTable { margin-bottom: 0; }
  }


  table.dataTable {
    .clearfix();

    // Common styles for all THs and TDs
    > thead > tr > th {
      // prevent sorting arrows from wrapping onto own line
      white-space: nowrap;

      // Match column headings with .panel-body for a seamless table header area
      background-color: darken(@panel-bg, 3%);
      border-top: none;

      font-weight: normal;
    }


    > thead,
    > tbody,
    > tfoot {
      > tr {
        > th,
        > td {
          // the table isn't in a panel-body, so it doesn't get padding like
          // the content above and below the table do. This lets the
          // horizontal table cell borders touch the panel's left and right
          // border. Here, we need to add extra padding to the first and last
          // cells in each table row so that their content aligns with the
          // content in the panel that is above and below the table.
          &:first-child {
            padding-left: @panel-padding;
          }
          &:last-child {
            padding-right: @panel-padding;
          }

          // Some numeric columns look better aligned with the right edge of
          // their heading text.  Account for the sort controls in the heading,
          // which have some width.
          &.numerical.align-to-sort-heading {
            padding-right: 2em;
          }
        }
      }
    }

    // Override dark DataTables default color
    > thead > tr > th,
    &.no-footer {
      border-color: @table-border-color;
    }

    > tbody {
      border-bottom: 1px solid @table-border-color;

      // Avoid adding an additional 1px border above the first data row
      tr:first-child > td {
        border-top: none;
      }

      // Heading rows span all cols; used to group rows. These styles
      // make them stand out better.
      td.grouped-by {
        font-weight: bold;
        font-size: 110%;
        text-align: center;
        vertical-align: bottom;
        height: 3em;
        border-bottom: 2px solid darken(@table-border-color, 10%);
      }
    }

    td.dataTables_empty {
      text-align: center;
    }

    // Override default arrow images; we provide our own
    > thead {
      .sorting,
      .sorting_asc,
      .sorting_desc {
        background-image: none;
      }
    }
  }

  .dataTables_info {
    // Keep footer on one line; new DT styles position this below the page size dropdown
    clear: none;

    .input-sm;
    float: left;

    // these four rules seem to do a good job of getting the info
    // text vertically aligned with neighbors
    font-size: inherit;
    line-height: inherit;
    padding: 5px;

    .table-items-shown {
    }

    .table-items-total {
      font-weight: bold;
    }
  }

  // Change cursor on disabled features (except for the first column which has a checkbox)
  .sorting_disabled:not(:first-child),
  .dataTables_paginate .btn.disabled {
    cursor: not-allowed;
  }


  // Scrollable DataTables
  // These get a dynamically generated thead that is fixed, while the body is scrollable.
  // This rule makes sure the original thead in the scrollBody is hidden.
  .dataTables_scrollBody table[data-table*=scrollable] thead {
    display: none;
  }


  /*
   * Processing indicator
   * Indicates that remote table content is being loaded
   */
  .panel {
    overflow: hidden; // make the border-radius on the panel clip the rectangular processing overlay
    position: relative;
  }

  // Override default styles set by DataTables. Ensure that the blocker overlay
  // covers the entire table.
  .dataTables_processing {
    background-color: rgba(255, 255, 255, 0.7);
    margin-top: 0;
    padding-top: 0;
    position: absolute;
    top: 0;
    left: 50%;
    width: 100%;
    height: 100%;
    z-index: 1000;

    /* We have to explicitly define a wrapping element with display: table
    * (versus letting the browser implicitly create one when it sees the
    * display: table-cell) because an implicit table element will have its width
    * and height set to auto, meaning that the size of the table cell will be
    * only as large as its content and not as large as the containing loading
    * div. */
    .wait_msg_wrapper {
      display: table;
      width: 100%;
      height: 100%;
    }

    .wait_msg {
      display: table-cell;
      text-align: center;
      vertical-align: middle;

      color: #444;
      font-size: 2em;
      font-weight: bold;
      text-shadow: white 0 0 0.5em;
    }
  }

}


// Override default white background of tables that are nested within tables
// that have hover style. Makes the hovered row look seamless.
table.dataTable.table-hover table tr {
    background-color: transparent !important;
}


/*
 * Sorting
 */
@disabled-sort-arrow-color: silver;
// the :hover selector and visibility rule make the sort arrows only show on
// hover, making for a cleaner display
.sorting::after               { content: " ▴▾"; visibility: hidden;  color: @disabled-sort-arrow-color; }
.sorting:hover::after         { content: " ▴▾"; visibility: visible; color: @disabled-sort-arrow-color; }
.sorting_asc::after           { content: " ▴"; }
.sorting_desc::after          { content: " ▾"; }
.sorting_asc_disabled::after  { content: " ▴"; color: @disabled-sort-arrow-color; }
.sorting_desc_disabled::after { content: " ▾"; color: @disabled-sort-arrow-color; }

.sorting,
.sorting_asc,
.sorting_desc {
  outline: none;
  cursor: pointer;
}



/*
 * Table controls and info fields
 */

// Table search field
.dataTables_filter {
  float: right;
  width: 20em;
  // vertically center in header row (not sure why this is required)
  line-height: 0;
  margin-left: 2em;

  // When search is active, highlight addon with the CB dark blue
  .active-search .input-group-addon {
    color: #fff;
    background-color: @blue-3;
    // the usual medium gray border looks bad/fuzzy against the dk blue
    border-color: @blue-3;
  }
}

// Added to all DataTables via sDOM. It's either empty or contains the toolbar
// for clickable DataTables.
.dataTables_toolbar {
  float: left;
  .btn-group {
    .btn {
      border-radius: 4px!important;
      margin: 0 .1rem;
    }
  }
}

// Dropdown for rows-per-page
.dataTables_length {
  float: left;
}

// Pagination controls
.dataTables_paginate {
  float: right;

  // Top pagination may be added to some tables for a more pleasant paging
  // experience when table height changes from page to page, but it takes up a
  // lot of space in others, so is not enabled by default.
  .dataTables-header & {
    clear: both;
    // take up minimal vertical space
    padding-bottom: 0;
  }
}


// elements having inputs or selects
.dataTables_filter,
.dataTables_length {
  input, select {
    .input-sm;
  }
}


/* Tweaks for `templates/common/datatable_form_dialog.html`, which has
 * `bPaginate` set to false, a `sScrollY` value, and `bScrollCollapse` set to
 * true.
 */

#dialog_datatable_form .dataTables_wrapper {
  .dataTables_scrollHead,
  .panel-body:empty {
    display: none;
  }

  // use shadows to indicate when items exist outside of the scrollable area's viewport
  .dataTables_scrollBody {
    .overflow-shadow();

    // override Bootstrap default border color
    border-bottom: 1px solid @table-border-color;
  }
}

// Our fnUpdateSelectionInfo plugin for dataTables adds this element
// to display the count of selected rows.
.selection-info {
  padding: 0;
  margin: 0;
  display: inline-block;
  float: left;

  // .label is used to show number of selected rows, but we customize the styles
  // to suit this context better than some of the Bootstrap defaults
  .label {
    font-size: 100%; // instead of 75%
    font-weight: normal; // instead of bold
  }
  .label-default {
    // lighter than Bootstrap default
    color: #999;
    background-color: #eee;
  }

  &.active-selection {
    .label-default {
      .cb-inverted;
    }

    // Show the clear-selection widget when there is a selection
    .clear-selection {
      display: inline-block !important;
    }
  }

  // For clickable data tables, this is the x glyphicon shown in the badge
  // indicating how many rows are selected.
  .clear-selection {
    // Hidden unless there is an active selection
    display: none;
    font-size: 80%;
    padding-left: .8em;

    .glyphicon-remove {
      color: #999;
    }

    &:hover {
      cursor: pointer;

      .glyphicon-remove {
        color: #fff;
      }
    }
  }
}
