.catalog-layout__wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    position: relative;
    height: 100%;
    min-height: 32rem;
    .catalog-filters {
        width: 25%;
        position: absolute;
        height: 100%;
        background-color: #ffffff;
        box-shadow: 0px 0px 2px 1px rgba(87,87,87,0.5);
        overflow-y: scroll;
        overflow-x: hidden;
        &--inner {
            background: #ffffff;
            .filters-module {
                background: transparent;
                margin: 0;
                border: none;
                &--head {
                    padding: 0;
                    background: #E9E9E9;
                    border-bottom:rgba(87,87,87,0.5);
                    .btn {
                        width: 100%;
                        display: flex;
                        flex-direction: row;
                        border: none;
                        background: transparent;
                        padding: .5rem;
                        i {
                            display: block;
                            margin-right: .5rem;
                            transition: transform .5s;
                        }
                        &.collapsed {
                            i {
                                transform: rotate(-90deg);
                            }
                        }
                    }
                }
                &--main {
                    padding: 1rem 2rem;
                }
            }
        }
    }
    .catalog-list__blueprints {
        flex-basis: 72%;
        margin-left: 25%;
        &.cards {
            display: flex;
            flex-wrap: wrap;
            .card {
                flex-basis: 24%;
                height: 16.5rem;
                margin: .25rem;
                min-width: 12rem;
                max-width: 14rem;
                a {
                    flex-direction: column;
                    align-items: flex-start;
                    padding: 1rem;
                    .ls-item {
                        width: 100%;
                        min-height: 4.25rem;
                        margin: 0 0 1rem;
                        &.catalog-list__logo {
                            .tile--logo {
                                width: 7rem;
                            }
                        }
                        &.catalog-list__config {
                            position: absolute;
                            top: 7rem;
                            left: 0;
                            right: 0;
                            height: 3rem;
                        }
                        &.catalog-list__details {
                            width: 75%;
                            align-self: flex-start;
                            margin-bottom: 0;
                            margin-top: 1rem;
                        }
                        
                    }
                }
                .catalog-list__icon {
                    .catalog-icon-gear {
                        top: 14rem;
                    }
                }
            }
        }
    }
}