/*
 * Styles for html.sortable plugin
 * https://github.com/voidberg/html5sortable
 */


// Drag handle may contain content
.sortable-handle, .move-to-top-or-bottom, .sort-seq {
  // Position and style the drag handle text content
  .panel.sortable .panel-heading > & {
    display: inline-block;
    font-size: 1.4em;
    color: #ccc;

    // Inset the icon and item number
    text-shadow: 0px 1px 1px rgba(255,255,255,.5), 0 -1px 1px rgba(0, 0, 0, .5);
  }

  // Slightly-inset handle
  i {
    text-shadow:
      0 1px 1px rgba(255, 255, 255, 0.4),
      0 -1px 1px rgba(0, 0, 0, 0.5);
  }
}

.move-to-top-or-bottom, .sort-seq {
  margin-right: 1em;
}

.sortable-handle {
  cursor: ns-resize;
  margin-right: 0.5em;
  &:hover {
    color: @blue-6 !important;
  }
}

.move-to-top-or-bottom {
  cursor: pointer;
  position: relative;

  .fa-angle-double-up {
    position: absolute;
    top: -25px;

    &:hover {
      color: @blue-6;
    }
  }

  .fa-angle-double-down {
    position: absolute;
    top: -5px;

    &:hover {
      color: @blue-6;
    }
  }
}

.sort-seq {
  cursor: text;

  .sort-seq-input {
    width: 60px;
  }
}

.compact-sortables .panel-heading span {
  font-size: 1em !important;

  .fa-angle-double-up {
    top: -17px;
  }
}

// Style of item as it is being dragged
.sortable-dragging {
}

.sortable-placeholder {
  border: 2px dashed @blue-6 !important;
}
