/*
 #f2f1f0 - lt grey
 #77787b - md grey: headings
 #7CA0C7 - md blue: table cells
 #447fc3 - dk blue
 #fcf6c2 - lt yellow: row highlight on hover
 #fce67e - lt orange: map, hover
 #f5c031 - md orange: map
 #f18216 - dk orange
 */
html, body {
    margin: 0;
    padding: 0;
}
/* darker emphasized headings/parts of headings */
h1 em, h2 em, h3 em, h4 em, h5 em, h6 em {
    color: #333;
}
li {
    list-style: none;
}
dl {
    margin: 0;
    overflow: auto;
}
dt {
    font-weight: bold;
    clear: both;
    float: left;
    /* Ensure consistent width. Looks good in most places, but override as
     * needed: too narrow causes wrapping, too wide separates dt & dd too much. */
    min-width: 20%;
    margin-right: .5em;
}
// Allow a dt to be wider if needed
dt.nowrap {
    width: auto;
}
dt.medium-tall {
    min-height: 4em;
}

.orderItemLists dt {
    /* 20% causes some DTs with long content to overlap DD */
    width: 30%;
}
/* Bring overview tab DTs closer to their DD values */
#tab-overview dt {
    width: 16%;
}

dd {
    padding-bottom: 1px; /* Just enough so .tiptext border isn't hidden */
}
dd p {
    margin: .3em 0;
}

.one-third {
    width: 33%;
}


ul.reduced,
ul.reduced li {
    padding: 0;
    margin: 0 !important;
}

dd {
    float: left;
    display: inline;
    margin: 0;
    max-width: 70%; /* must be updated whenever width of <dt> is modified */
}

/* RH list details column */
td.details {
    width: 180px;
}
td.details dt,
td.details dd {
    display: inline-block;
    float: none;
    width: auto;
}

/* lists in cloudbolt-palette */
dl.sc {
    background-color: #C5DEFC !important;
    border: 1px solid #9ebbdb !important;

    background: -moz-linear-gradient(top,  #e2edf7 0%, #c4dbef 100%) !important; /* FF3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#e2edf7), color-stop(100%,#c4dbef)) !important; /* Chrome,Safari4+ */
    background: -webkit-linear-gradient(top,  #e2edf7 0%,#c4dbef 100%) !important; /* Chrome10+,Safari5.1+ */
    background: -o-linear-gradient(top,  #e2edf7 0%,#c4dbef 100%) !important; /* Opera 11.10+ */
    background: -ms-linear-gradient(top,  #e2edf7 0%,#c4dbef 100%) !important; /* IE10+ */
    background: linear-gradient(top,  #e2edf7 0%,#c4dbef 100%) !important; /* W3C */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e2edf7', endColorstr='#c4dbef',GradientType=0 ) !important; /* IE6-9 */
}
dl.sc dt.first {
    margin-top: 0;
}

dl.sc dt {
    background-color: #b8d1ec;
    border-right: 1px solid #9ebbdb;
    border-bottom: 1px solid #9ebbdb;
    margin: 1px 1em 0 0;
    padding: 2px 1em;
    line-height: 1.5em;
}
dl.sc dt:last {
    border-bottom: none;
}

dl.sc dd {
}
dl dt.head {
    width: 100% !important;
    text-align: center;
    margin: 0;
    padding: 0.25em 0;
    border: none;
}
dl.sc dt.head {
    background-color: #9ebbdb;
}
dd.head {
    display:none;
}

/* cleaner dialog style */
.orderDialog .btn-toolbar,
.action_dialog_body .buttons,  /* these are the iframe's submit buttons */
.ui-dialog .ui-dialog-buttonpane {
    border-top: none;
    background: none;
    /* center the buttons */
    text-align: center;
}
/* center dialog submit buttons */
.ui-dialog .ui-dialog-buttonpane .ui-dialog-buttonset {
    float: none;
}
.ui-dialog .ui-dialog-buttonpane .ui-dialog-buttonset .ui-button:last {
    margin-right: 0;
}

.ui-dialog .ui-dialog-titlebar {
    border: none;
    color: #fff;

    background: rgb(154,188,224); /* Old browsers */
    background: -moz-linear-gradient(top,  rgba(154,188,224,1) 0%, rgba(124,160,199,1) 82%); /* FF3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(154,188,224,1)), color-stop(82%,rgba(124,160,199,1))); /* Chrome,Safari4+ */
    background: -webkit-linear-gradient(top,  rgba(154,188,224,1) 0%,rgba(124,160,199,1) 82%); /* Chrome10+,Safari5.1+ */
    background: -o-linear-gradient(top,  rgba(154,188,224,1) 0%,rgba(124,160,199,1) 82%); /* Opera 11.10+ */
    background: -ms-linear-gradient(top,  rgba(154,188,224,1) 0%,rgba(124,160,199,1) 82%); /* IE10+ */
    background: linear-gradient(to bottom,  rgba(154,188,224,1) 0%,rgba(124,160,199,1) 82%); /* W3C */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#9abce0', endColorstr='#7ca0c7',GradientType=0 ); /* IE6-9 */
}
/* CSS for scrollable form of our dialog template: */
.checkbox-list.scrollable,
.ui-dialog .scrollable {
    border: solid 1px #ccc;
    overflow: auto;
}

a:active,
a:visited,
a {
	text-decoration: none;
}
a:hover {
	color: #447fc3;
	text-decoration: underline;
}
a.nolink:active,
a.nolink:visited,
a.nolink:hover,
a.nolink {
	color: #000 !important;
	text-decoration: none;
}
a.external {
    background: url(../img/icons/externallink.png) no-repeat top right;
    padding-right: 16px;
}
a.button {
    color: #555; /* restore UI theme color */
    text-decoration: none;
}
a.button:hover {
    color: inherit;/* get color from jq UI, not 447fc3 */
}
/* disabled button link */
a.button[disabled]:hover {
    cursor: auto;
}
a.disabled:hover {
    cursor: default;
    text-decoration: none;
}
a.disabled {
    color: #999 !important;
}
/* link_or_label when str(obj) is not set */
a.no-name {
    font-style: italic;
}

#container {
    position  : relative;
    width     : 960px;
    padding   : 0;
    margin    : auto;
}

#popout {
    color: #447fc3;
    background-color: #7CA0C7;
    text-shadow: 0px 1px 0px rgba(255, 255, 255, 0.75);
}

#navigation {
    height: 27px;
    text-align : left;
}
#navigation span {
    display: inline-block;
    vertical-align: middle;
}
#navigation a {
    font-size: 16px;
    display: inline-block;
    padding: 4px 4px 0;
    height: 20px;
    margin-right: 3px;
    border-bottom: 3px solid transparent;
	text-decoration: none;
}
#navigation a,
#result_list th a,
a.ui-button {
    color: #77787b !important; /* override default a color */
}
#navigation a.nav-dashboard {
    vertical-align: top;
    height: 24px;
    /* remove the bottom border effect */
    border: none;
    border-right: 1px solid #bfbeba;
    padding: 3px 4px 0 8px;
    /* match nav background corners */
    border-top-left-radius: 6px;
    border-bottom-left-radius: 6px;
}
#navigation a.nav-dashboard:hover {
    background-color: #ddd;
    border-bottom: none;
}

.ui-button {
    min-height: 18px;
}

#result_list th.sorted a,
a.ui-button.ui-state-hover {
    color: #222 !important; /* override default a color */
}

.security_message {
    text-align : center;
    color      : black;
    font-weight: bold;
    padding    : 0.25em 0;
}

.hidden,
.tabs-hide { /* added a second class to avoid conflict with initializeTabs */
    display: none;
    visibility: hidden;
    height: 0;
}

/* used in formsets */
form .toBeRemoved td {
    background-color: #f99 !important;
}

.power_status_size {
    width: 15px;
    height: 15px;
}

.power_status_center {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.floatright {
    float: right;
}

#edit_envs {
    margin: 0 90px 10px 130px;
}

/* smaller buttons */
.groups #tab-overview .ui-button {
}
.groups #tab-overview .ui-button-text {
    padding-top: 3px;
    padding-bottom: 3px;
}
.groups th.email {
    width: 300px;
}

#networks th.nic,
#networks td.nic {
    text-align: center;
    width: 15%;
}


.selector h2 {
    font-size: 100%;
}
.detailLabel,
.detailValue {
    line-height: 14pt;
    display: table-cell;
}
.detailLabel {
    padding         : 0.25em;
    font-weight     : bold !important;
    width           : 20%;
    background-color: #e9e9e9;
    color           : #666;
    vertical-align  : top;
}

.detailValue {
    width           : auto;
    color           : #000;
    background-color: #fff;
    padding         : 0 0.25em;
}

.detailValue ul {
    list-style: inside;
}

td.detailLabel {
    display: table-cell;
}

table a.detailLink {
    display: block;
}

.pre,
pre {
    white-space: pre-wrap;       /* css-3 */
    white-space: -moz-pre-wrap;  /* Mozilla, since 1999 */
    white-space: -pre-wrap;      /* Opera 4-6 */
    white-space: -o-pre-wrap;    /* Opera 7 */
    word-wrap  : break-word;     /* Internet Explorer 5.5+ */
}
.pre {
    font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', 'Consolas', 'source-code-pro', monospace;
}

#changelist,
#changelist .results {
    overflow: auto;
}

/* clickable behavior, selectable rows */
.selector select {
    width: 100%;
}

.grey {
    color: gray;
}

/* Icons tied to IDs of group type, in case names change. Managers ought to be able to pick icons eventually. */
.treeTable tr {
    height: 26px;
}

.treeTable tr td.first {
    font-size: 12px;
    font-weight: bold;
    line-height: 13px;
    padding-left:20px; /* room for icon */
}

/* this table is not on a white bg, so outline for definition */
#groupTree {
    border: 1px solid #aaa;
}

.servers .power_state {
    text-align:center;
}
.buttons {
    clear: both;
    margin: .5em 0 1em;
}
.buttons .btn {
    margin-right: .5em;
}
#paramForm .buttons {
    text-align: left;
}

/* just assign width to this element and it'll center in its container */
.centered {
    margin: auto;
}
.servers .left {
    width: 200px;
    float: left;
    margin-right: 2em;
}
#server-overview dl {
    font-size: 75%;
}
#server-overview dt {
    width: 50px;
}

/* So that the iframe dialogs don't have the blue background: */
body.action_dialog_body {
    background: white;
}

.clone_dialog {
    width: 400px !important;
}
.server_change_resources {
}

/* formset forms added dynamically */
.new-dynamic-form        td,
.new-params-dynamic-form td {
    background-color: #e6f8f0 !important;
}

/* power_status value exists for sorting only */
td.power b {
    display: none;
}

.unsaved-changes-message {
    margin: .5em 1em;
    padding: .3em 1em;
    text-align: center;
    /* Show this message next to page header, without offsetting subsequent content */
    width: 65%;
    float: right;
}
.unsaved-changes-message em {
    font-weight: bold;
}
.unsaved-changes input[value="Save changes"] {
    /* highlight */
    border: 2px solid orange;
}


/* In dialogs, let table take on width of its content, not its container */
form#action_form table.sc {
    width: auto !important;
}


.stats td.bool {
    width: 50px;
}

#orderItems .panel {
    width: 75%;
    margin: auto;
    margin-bottom: 15px;
}

.panel-body p.attention {
    font-weight: bold;
}

/* MESSAGES & ERRORS */
/* Derived from admin/css/base.css */
ul.messagelist {
    padding: 0;
    margin: 0 2em;
}
ul.messagelist li:first-child {
    margin-top: .5em;
}
ul.messagelist li:last-child {
    margin-top: .5em;
}
ul.messagelist li {
    font-size: 12px;
    display: block;
    padding: 4px 5px 4px 25px;
    margin: 0 0 3px 0;
    border-bottom: 1px solid #ddd;
    color: #666;
    background: #ffc url(../img/icons/accept.png) 5px .3em no-repeat;
}

ul.messagelist li.warning{
    background-image: url(../img/icons/warning.gif);
}
ul.messagelist li.error{
    background-image: url(../img/icons/error.gif);
}
ul.messagelist li.info {
    background-image: url(../img/icons/information.png);
}
ul.messagelist li.confirm {
    background-image: url(../img/icons/question.png);
}

.right {
    float: right;
    display: inline-block;
}

/* align the readonly field with neighborhing input */
.parameters-used .readonly {
    padding: .25em 0;
    display: inline-block;
}

ul.actions {
    margin: .5em auto;
}
ul.horizontal li {
    display: inline-block;
}

.env_rates,
.env_rates_breakdown {
    width: 100%;
}
tr.editable.hover {
    background: #cbe1f4;
    margin:1em;
    border-radius: 6px;
}


/* For multi-column layouts, apply this rule to every column div, and one
 * of the width rules below to create (.thirds-1, etc) */
.column {
    width: 45%;
    display: inline-block;
    vertical-align: top;
    /* Ignore border & padding when calculating width */
    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box;    /* Firefox, other Gecko */
    box-sizing: border-box;         /* Opera/IE 8+ */
}
.half {
    width: 50%;
}
.thirds-1 {
    width: 33%;
}
.thirds-2 {
    width: 66%;
}

th.numerical,
td.numerical {
    text-align: right !important;
}

/* used in group-tree */
td.quota-view {
    text-align: center;
    color: gray;
}

#ldap-settings dt {
    width: 170px; /* to prevent wrapping in the "header" column */
}

/* for browsers that don't support the seamless attribute (everything but
 * Chrome), at least emulate seamless style */
iframe.seamless {
    background-color: transparent;
    border: 0px none transparent;
    padding: 0px;
    overflow: hidden;
}


/* Import AMIs dialog */
.ami-selection-section td {
    vertical-align: top;
}
.ami-selection-section td.selector {
    /* minimize the checkboxes column */
    width: 12px;
}

.import-amis #tally-message {
    width: 12em;
    font-weight: bold;
}

/* Left side of the map view */
.left-col {
    /* This prevents the map from ever getting misaligned */
    max-width: 230px;
}

/* adjust column widths in AMIs table */
.resourcehandlers table.templates td.id {
    white-space: nowrap;
}
.resourcehandlers table.templates td.name {
    min-width: 12em;
}

table#tech-params-table td.name,
table#regions td.name {
    white-space: nowrap;
}

/* Dashboard */
ul.simple li,
table.simple th,
table.simple td {
    padding: .2em;
}
.panel-heading .icon {
    /* align icon with text */
    position: relative;
    top:-2px;
}
.panel.blueprints td.image {
    text-align: center;
}
.panel.activity strong a {
    margin-top: .5em;
}
.panel.activity p {
    margin: 0 0 1em;
}
.panel#reports ul {
    margin: 0;
    padding: 0;
}
.panel#reports li {
    /* as tall as the hidden buttons */
    line-height: 2em;
}
.panel#reports li .btn {
    display: none;
}
.panel#reports li:hover .btn {
    display: block;
}

table.simple {
    width: 100%;
    border-collapse: collapse;
}
table.simple th {
    text-align: left;
    color: #666;
}
