/**
 * Visual representation of a server
 *
 * See also cards.less
 */

a.server-card-link {
  display: inline-block;
  max-width: 20rem;
  // Wrap a list of cards with `.cards-block` to make them block-level elements
  .cards-block & {
    display: block;
  }
}

.server-card {
  display: block;
  text-align: center;

  // Cards can be layed out inline, for density, by wrapping them in .inline-cards
  .inline-cards & {
    float: left;
    margin-right: 1em;
    margin-bottom: 1em;
  }

  // Server cards that are links automatically get wrapped in a box with hover effects

  a > & {
    .card-box;
  }

  a:hover > & {
    .card-box-hover;

    .hostname {
        color: @blue-2;
    }
  }

  .hostname {
    .h5;
  }

  // Lay out the graphics
  .visual {
    position: relative;
    width: 128px;
    height: 140px;
    margin: 0 auto;

    .blade-icon {
      position: absolute;
      top: 0;
      left: 0;
    }

    .family-icon {
      position: absolute;

      /* position left of on/off indicator */
      top: 86px;
      left: 20px;
    }

    // This is only used for .tiny cards
    .power-state-indicator {
      display: none;
    }
  }

  // Small representation suitable for table rows, etc.
  &.tiny {
    display: inline-block;

    .hostname {
      float: right;
      margin: .5em;
    }

    .visual {
      display: inline;
    }

    .visual {
      height: 40px;
      position: relative;

      .blade-icon {
        // Undo positioning
        position: relative;
        top: 0;
        left: 0;
      }

      .family-icon {
        // Undo positioning
        position: absolute;
        top: -4px;
        left: 8px;
      }
    }

    .power-state-indicator {
      display: block;
      height: 3px;
      width: 7px;
      position: absolute;
      top: 15px;
      left: 12px;
    }

    // server card has a class == `power_status|lower`
    &.poweron .power-state-indicator {
      background-color: #0ef40e;
    }
    &.unknown .power-state-indicator {
      background-color: #ff8200;
    }
  }
}


// "+3 more" element that gets styled like server cards
.server-card-ellipsis {
  .card-box;

  &:hover {
    .card-box-hover;
  }
}


// Fix a display problem in short windows: hovering over these anchors
// causes the window scroll position to change. Could be an interaction
// with tooltip feature.
.inline-cards > a {
  display: block;
}


// Power state is rendered in the card tooltip as well as on server details page
.power-state {
  font-weight: bold;

  &.poweron { color: #0ef40e !important; }
  &.poweroff { color: #999 !important; }
  &.unknown { color: #ff8200 !important; }
}


// Server detail view and Resource detail view
#content.servers,
#content.resources {
  // The left-hand column
  .col-sm-3 {
    // These are the server action buttons
    // Button groups shrink to fit the widest label by default; here we want
    // them to have a consistent width relative to their container instead.
    .btn-group-vertical {
      width: 80%;

      a {
        // Allow wrapping to prevent long button labels from spilling out of
        // the box
        white-space: normal;

        // icons
        i {
          padding-right: 8px;

          // Glyphs vary in width so fix width for consistency
          width: 24px;
          display: inline-block;
          text-align: center;
        }

        // Make Stacked icons the same size as others
        .fa-stack {
          font-size: 60%;
        }
      }
    }
  }
  #resource-details-sidebar {
    .visual {
      font-size: 5rem;
      padding: 1rem;
    }
  }
}


// Server list view

#filters-container {
    li.filter-field {
        display: inline-block;
        /* Accomodate 5 SelectizeMultiples */
        width: 19%;
        vertical-align: top;
    }

    /* the visible labels for each filter */
    li.filter-field > label {
        display: block;
    }
}


// Power schedule form

.power-schedule {
  margin-top: 20px;
  padding-left: 20px;
  padding-right: 20px;

  .delete-schedule {
    display: block;
    position: relative;
    float: right;
    margin-top: 10px;
    width: 9px;
    height: 9px;
    background-image: url("../img/png/close-dark.png");
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
  }

  .sep {
    margin: 0 10px;
  }
}

button#add-power-schedule {
  margin: 10px 0;
}
