.btn-toolbar {
  margin-bottom: 15px;
  margin-left: 0;
  // Except those in panel footers
  .panel-heading &,
  .panel-footer & {
    margin-bottom: 0;
  }
}

.btn-toolbar {
  // Space out series of button groups.
  // We override the method used by Bootstrap, opting to add a left margin to
  // all elements and remove them from the first, instead of using a sibling
  // selector to add it to the second. This is because we've added a
  // btn-wrapper class and want to avoid an n^2 explosion of sibling selectors.
  > .btn,
  > .btn-wrapper,
  > .btn-group {
    margin-left: 2px;
  }
}
