// Most tooltips want the dotted underline affordance but there are a few
// exceptions. Elements that already have a border don't want it changed
// or taken away (by the no-tooltip-affordance rule) so they need to be
// excluded by default.
// This LESS is compiled to one (possibly expensive) CSS rule
[data-toggle="tooltip"] {
  &:not(img) {
  &:not(.no-tooltip-affordance) {
  &:not(.btn) {
  // .btn-wrapper is used to add tooltips to disabled buttons
  &:not(.btn-wrapper) {
  &:not(.icon) {
  // quota limit bar charts
  &:not(.bar) {
  // job progress bars
  &:not(.progress) {
  &:not(.glyphicon) {
    .tooltip-affordance;
    /* Ensure tooltip hint is close to the text */
    padding-bottom: 0;
  }}}}}}}}
}

.tooltip-affordance {
  border-bottom: 1px dotted;
}

.tooltip {
  font-size: @font-size-base;

  // Above CB navbar submenus
  z-index: 5000;

  &.in {
    opacity: 1;
  }

  // Server pictograph tooltips show tabular data
  th,
  td {
    padding: @table-condensed-cell-padding;
    text-align: left;
  }

  // Text in our tooltips should be readable against the dark blue
  h1, h2, h3, h4,
  a {
    color: #fff !important;
  }
}

#tooltip-container {
  width: 100%;
  z-index: 1050;
  .row {
    margin-top: 5px;
    margin-bottom: 5px;
    margin-left: 5px;
    text-align: left;
  }
}
