/* forms.css - styles related to form elements and user input
 */
form dt {
    width: 150px;    /* changed from 130 to 150 for consistency with Django forms */
}
form ul {
    margin: 0; /* Margin no longer needed, because we are adding padding to the parent tag */
}


form label.required,
form .field-label.required {
    font-weight: bold;
}

/* reset these fields to be more homogenous */
input[type=text],
input[type=password],
input[type=colorpicker],
select,
textarea {
    /* Ignore border & padding when calculating width */
    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box;    /* Firefox, other Gecko */
    box-sizing: border-box;         /* Opera/IE 8+ */
    border: 1px solid #bbb;
    padding-top: 0.25em;
    padding-bottom: 0.25em;
}

/* fields in our dialog forms expand to fit table cell they're in */
form table.labels-left input[type=text],
form table.labels-left select,
form table.labels-left textarea,

#xenresourcehandler_form label,
#awshandler_form label,
#vsphereresourcehandler_form label,
#cobblerserver_form label{
   width : 350px;
}

#xennetwork_form label,
#cobblerprofile_form label,
#preconfigurationvaluesetrate_form label,
#customfield_form label,
#environment_form label{
   width : 150px;
}

#license_form label,
#licensepool_form label{
   width : 100px;
}

form .selector-available p.selector-filter {
    display: inline-block;
    width: 262px;
    height: 26px;
    margin: 0;
}

.selector-available p.selector-filter input {
    display: inline-block;
}

form .selector-available h2 {
 margin: 10px 0 0;
}

form .selector-chosen h2 {
 margin: 10px 0 0;
}

form .selector-chosen p.selector-filter {
    display: inline-block;
    width: 258px;
    height: 26px;
    margin: 0;
    vertical-align: bottom;
}

select,
input,
textarea {
    margin: 0;
}

input[disabled],
input[readonly] {
    /* this is the same style the Chrome UA gives disabled inputs. We give it
     * to readonly inputs, which have no special default styling, so that users
     * have a visual indicator that they can't edit the field. */
    background-color: rgb(235, 235, 228);
}

.loginout form label {
    text-align: right;
    width: 80px;
}
.loginout form input {
    font-size: 100%;
    width: 160px;
}
.loginout form input.button {
    width: auto;
}

.servers .resources input {
    width: 45px;
}

/* override label style for boolean CFs and other checkboxes */
label.checkbox {
    font-style: normal;
    padding-left: 20px;
}
.ui-datepicker-trigger {
    /* show that calendar icon is clickable */
    cursor: pointer;

    /* create a pleasant vertical-alignment with the neighboring text input */
    height: 26px;
    vertical-align: bottom;
}

ul.errorlist {
    color: red;
    margin: 0 !important;
    padding: 0 !important;
}

.errorlist li a {
 	color: white;
    text-decoration: underline;
}

td ul.errorlist {
    margin: 0 !important;
    padding: 0 !important;
}

td ul.errorlist li {
    margin: 0 !important;
}

ul.errorlist ul.with-dots li {
    list-style: disc;
}

ul.errorlist ul.inherit-link-color a {
    color: inherit;
}

.errors {
    background: #ffc;
}

.errors input,
.errors select,
.errors textarea {
    border: 1px solid red;
}

textarea:disabled {
    background-color: #ddd;
    overflow:auto;
}


#preconfig-options-table input {
    width: 120px;
}

#action_form select#id_param {
    width: 100%;
}

/* Group > Users tab: align the user search field with siblings */
input.find-user {
    margin-top: 8px;
    width: 50%;
    vertical-align: middle;
}

/* controls for checking/unchecking all in a row or column */
.check-all {
}
.check-row {
}
.check-column {
}

/* Form layout with labels in left column and fields on right,
 * and where we don't have much control over the output, i.e.
 * from form.as_table().  */
table.labels-left tbody th {
    width: 30%;
    text-align: left;
}
table.labels-left tbody th,
table.labels-left tbody td {
    vertical-align: top;
    padding: .25em;
}

/* Sharper relief against QS forms' white bg */
.quick_setup .checkbox-list {
    border: 1px solid #ccc;
}
