.legacy {
  padding: 0 1rem;
  .panel {
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.25);
    border-radius: 5px;
    .panel-heading {
      background-color: #fff;
      border-color: #fff;
      padding: 0;
      .panel-title {
        font-size: 1.125rem;
        border-bottom: 2px solid #cee7ff;
        padding: 0.5rem 1rem;
        width: 16.25rem;
      }
      .pull-right {
        grid-column-start: 10;
        grid-column-end: 13;
        text-align: end;
        align-self: center;
        padding-right: 1rem;
      }
    }
    &-footer {
      background-color: #fff;
      border-top: none;
    }
  }

  .panel-body {
    &.extra-padding {
      padding: 15px 75px;
      p {
        &.extra-margin {
          margin: 1.25rem 0;
        }
      }
    }
  }
  > .panel-body + .panel-body {
    border-top: 1px solid @table-border-color;
  }
}

.panel-footer > .btn-toolbar {
  // To make 'Cancel'/'Save Changes' buttons in pages like Misc. Settings be on
  // the right, like they are in dialogs.
  text-align: right;
}

// To make bootstrap panel collapsing play nice with vue panel collapsing. Vue has
// a rule that hides when there's no "show" class, but bootstrap uses the "in"
// class instead so we need to make sure that's displayed (basically copied from
// boostrap css and marked important)
.collapse.in {
  display: block !important;
}

// Specifically setting user-select here to work around a Chromium bug BP item
// panels are <li> elements which we set to "user-select: None". The bug is that
// child elements inherit this setting. But we don't need them to. See
// https://chromium.googlesource.com/chromium/src/+/b01af0b296ecb855aac95c4ed335d188e6eac2de
li .panel .panel-body {
  user-select: text;
}
