dl {
    dt, dd {
        // More spacing than default 1.4
        line-height: 1.8em;
    }
}

dl.width-auto { dt { width: auto !important; } }
dl.dt-20 { dt { width: 20% !important; } }
dl.dt-25 { dt { width: 25% !important; } }
dl.dt-30 { dt { width: 30% !important; } }
dl.dt-50 { dt { width: 50% !important; } }

// Taller lines keep dt and dd vertically aligned when one is taller, e.g.
// gravatars and hardware icons on server details.
dl.line-height-32 { dt, dd { line-height: 32px; }}

/* Definition lists where terms are on a separate line from definitions */
dl.vertical {
  padding: .2em;

  dt,
  dd {
    float: none;
    width: auto;
  }

  dt:not(:first-child) {
    margin-top: 1em;
  }
}

.new_row_set {
  padding-bottom: 1em;
}

// Subtle index numbers on some ordered lists, e.g. service items.
ol.with-numbers {
  position: relative;
  counter-reset: li;
  .list-unstyled;

  > li {
    counter-increment: sequence-number;

    &::before {
      content: counter(sequence-number);
      font-size: 1.5em;
      color: #ccc;
      //padding: .3em .5em;
      //float: left;
      width: 45px;
      height: 30px;

      display: inline-block;
      position: relative;
      top: 0;
      left: 0;
    }

  }
}


// General list styles for managing the distance between list items
.li-spacing-1 > li { margin-bottom: 1em; }
.li-spacing-2 > li { margin-bottom: 2em; }
.li-spacing-3 > li { margin-bottom: 3em; }
.li-spacing-4 > li { margin-bottom: 4em; }


ul.bullets > li {
    list-style-type: disc;
}
ol.bullets > li {
    list-style-type: upper-roman;
}

// Lists of buttons. Legacy; probably can be replaced with btn-group.
ul.button-list {
    text-align: center;
}
ul.button-list a.btn {
    width: 20em;
    margin-bottom: .6em;
}


// General hover style for list items
ol.hover-highlight,
ul.hover-highlight {
  li {
    border: 1px solid transparent;
    border-radius: @border-radius-small;

    &.hover,
    &:hover {
        background-color: @table-bg-hover;
        border: 1px solid #ddd;
        transition: .2s linear all;
    }
  }
}


ol.hierarchical,
ul.hierarchical {
  @width-per-level: 28px;

  li {
    padding: 4px;
  }

  li.current {
    background-color: @highlight-bg;
  }

  li.depth-0 { }
  li.depth-1 { padding-left: 1 * @width-per-level; }
  li.depth-2 { padding-left: 2 * @width-per-level; }
  li.depth-3 { padding-left: 3 * @width-per-level; }
  li.depth-4 { padding-left: 4 * @width-per-level; }
  li.depth-5 { padding-left: 5 * @width-per-level; }
  li.depth-6 { padding-left: 6 * @width-per-level; }
  li.depth-7 { padding-left: 7 * @width-per-level; }
  li.depth-8 { padding-left: 8 * @width-per-level; }
  li.depth-9 { padding-left: 9 * @width-per-level; }
}

ol#sequenced-items > li > .panel {
  // Special case: More compact list of panels (default is 20px)
  margin-bottom: 4px;
}

ol.compact-sortables > li {
   // we make the action input sequencing smaller than normal to take up less real estate
   // on the action detail page

   .sortable-handle {
     font-size: 1em;
   }

   > .panel {
     margin-bottom: 4px;

     .panel-heading {
        padding: 2px 2px 2px 1em;
     }
  }
}

/* Style checkbox-list widgets similar to native multi-select control.
 *
 * These are also clickable, so inherit some styles from that.
 *
 * For fixed-height content that may overflow, give users a hint about this
 * by setting height such that part of the next line shows.
 */
ul.checkbox-list {
  @native-selection-blue: #3875d7;

  background-color: #fff;
  padding: 0;

  li {
    padding: 0 0.5em;
    line-height: 1.5em;

    &.selected {
      background-color: @native-selection-blue !important;
      color: #fff;

      label {
        color: #fff;
      }
    }

    &:hover {
      background-color: @table-bg-hover !important;

      &.selected {
        background-color: darken(@native-selection-blue, 8%) !important;
      }
    }
  }

  label {
    padding: 0;
    margin: 0;
  }

  /* Height is set to show desired number of full lines followed by one
  * partial to hint at scrollability for more content.
  *
  * A good height has been experimentally determined to be 1.77em * (lines +
  * 0.5). A less arbitrary height can be arrived at when the styles affecting
  * the components in a checkbox list are simplified. */
  @checkbox-list-line-height-factor: 1.77em;
  &.lines-3  { height: @checkbox-list-line-height-factor * (3 + 0.5); }
  &.lines-4  { height: @checkbox-list-line-height-factor * (4 + 0.5); }
  &.lines-5  { height: @checkbox-list-line-height-factor * (5 + 0.5); }
  &.lines-6  { height: @checkbox-list-line-height-factor * (6 + 0.5); }
  &.lines-8  { height: @checkbox-list-line-height-factor * (8 + 0.5); }
  &.lines-10 { height: @checkbox-list-line-height-factor * (10 + 0.5); }
}
