// Styles for jQuery clearable plugin


/* Clearable looks best when the trigger is positioned just inside the input
 * element, positioned absolutely.  To make this work, the input & clearable
 * trigger need to be wrapped by this.
 */
.clearable-wrapper {
  display: inline-block;
  position: relative;
}


// Glyph & link to clear an input field
.clearable-trigger {
  position: absolute;
  top: 9px;
  right: 7px;
  color: #ddd;
  // Higher than elements in .input-group .form-control, e.g. datatable search
  // inputs, which have a z-index of 2
  z-index: 100;

  // Bootstrap Addons on the right side of an input act as padding. They cover
  // the right part of the input so the clearable trigger needs to be moved
  // left of them.
  .input-group            & { right: 50px; }
  .input-group.addon-left & { right: 7px; }
}


// Make it act clickable
.clearable-trigger:hover {
  color: @gray-darker;
  cursor: pointer;
}


// Remove IE native clearable and password-revealing controls if
// an input also has clearable behavior.
input.clearable::-ms-clear {  display: none; width : 0; height: 0; }
input.clearable::-ms-reveal {  display: none; width : 0; height: 0; }
