/* Component containers
----------------------------------*/
.ui-widget { font-family: "Lucida Sans Unicode","Lucida Grande", Verdana, Arial, sans-serif; font-size: 90%; }
.ui-widget .ui-widget { font-size: 1em; }
.ui-widget input, .ui-widget select, .ui-widget textarea, .ui-widget button { font-family: "Lucida Sans Unicode","Lucida Grande", Verdana, Arial, sans-serif; font-size: 1em; }
.ui-widget-header { border: 1px solid #aaaaaa; background: #e0dfdc url(images/ui-bg_highlight-soft_75_e0dfdc_1x100.png) 50% 50% repeat-x; color: #222222; font-weight: bold; }
.ui-widget-header a { color: #222222; }

/* Interaction states
----------------------------------*/
.ui-state-default, .ui-widget-header .ui-state-default { border: 1px solid #bfbeba; background: #e6e6e6 url(images/ui-bg_glass_75_e6e6e6_1x400.png) 50% 50% repeat-x; font-weight: normal; color: #555555; }
.ui-state-default a, .ui-state-default a:link, .ui-state-default a:visited { color: #555555; text-decoration: none; }
.ui-state-hover, .ui-widget-header .ui-state-hover, .ui-state-focus, .ui-widget-header .ui-state-focus { border: 1px solid #999999; background: #8db9e6 url(images/ui-bg_glass_75_8db9e6_1x400.png) 50% 50% repeat-x; font-weight: normal; color: #212121; }
.ui-state-hover a, .ui-state-hover a:hover { color: #212121; text-decoration: none; }
.ui-state-active, .ui-widget-header .ui-state-active { border: 1px solid #aaaaaa; background: #ffffff url(images/ui-bg_glass_65_ffffff_1x400.png) 50% 50% repeat-x; font-weight: normal; color: #212121; }
.ui-state-active a, .ui-state-active a:link, .ui-state-active a:visited { color: #212121; text-decoration: none; }
.ui-widget :active { outline: none; }

/* Interaction Cues
----------------------------------*/
.ui-state-highlight, .ui-widget-header .ui-state-highlight  {border: 1px solid #f5c031; background: #fce67e url(images/ui-bg_highlight-soft_55_fce67e_1x100.png) 50% top repeat-x; color: #363636; }
.ui-state-highlight a, .ui-widget-header .ui-state-highlight a { color: #363636; }
.ui-state-error, .ui-widget-header .ui-state-error {border: 1px solid #cd0a0a; background: #fef1ec url(images/ui-bg_inset-soft_95_fef1ec_1x100.png) 50% bottom repeat-x; color: #cd0a0a; }
