.report-thumbnail {
  background-color: #fff;
  border: 1px solid @border-color;
  border-radius: @border-radius-small;
  margin: 6px;
  padding: 6px;

  // Larger thumbnails in report list
  #reports & {
    float: left;
    margin: 0 12px 12px 0;
    padding: 12px;
    width: 128px;
    // BestPractice: CSS drop shadows
    // Use a negative value for the 4th px dimension to scale down the shadow size.
    // A nice modern drop shadow effect where the shadow's dimensions are smaller
    // than the thing casting it.  This looks nicer than a plain same-sized shadow
    // where the dark areas extend on the sides.  It makes the light source appear
    // more ambient and diffuse.
    .box-shadow(0 7px 25px -11px rgba(0,0,0,0.5));
  }

  // Smaller thumbnails in dashboard card and top navbar
  [data-topnav=dashboard] &,
  ul.nav ul.dropdown-menu & {
    width: 64px;
    // BestPractice: CSS drop shadows
    // Use a negative value for the 4th px dimension to scale down the shadow size.
    // A nice modern drop shadow effect where the shadow's dimensions are smaller
    // than the thing casting it.  This looks nicer than a plain same-sized shadow
    // where the dark areas extend on the sides.  It makes the light source appear
    // more ambient and diffuse.
    .box-shadow(0 5px 25px -7px rgba(0,0,0,0.5));
  }
}


#reports {
  li {
    clear: both;
    padding: 8px;
    margin-bottom: 16px;
  }
}


#cost-dashboard {
  background-color: #fff;
  border: 1px solid @border-color;
  border-radius: @border-radius-large;
  min-height: 1200px;
  padding: 15px;

  .chart {
    border: 1px solid #eee;
    border-radius: @border-radius-small;
    padding: 8px;

    .chart-controls {
      margin-top: 20px;
    }
  }
}


#custom-server-report-form {
  table#filters {
    width: 100%;

    td {
      vertical-align: middle;
    }

    td.attr,
    td.values {
      // Long enough for placeholder text to render well
      min-width: 12em;
    }

    td.operator {
      min-width: 6em;
      text-align: center;
    }

    // Override a min-width style set directly on date picker inputs because it
    // causes the filters table layout changes when these are added & removed.
    input.large-width,
    input.render_as_datepicker {
      min-width: auto !important;
      width: auto !important;
    }

    .formset-controls {
      border-top: @border-color;
      background-color: #fff;
    }
  }
}


