// Custom server report
#report-container {
  // min-height so the block effect is visible
  min-height: 400px;
}

// This is to override some styles for standalone emailed reports
body.emailed-report {
  margin: 12px;

  .tab-content > .tab-pane {
    display: block;
  }

  // Should hide report controls
  .btn-toolbar {
    display: none;
  }
}
