.csscolumns {
  .masonry(@column-count) {
    .column-count(@column-count);
    .column-gap(@grid-gutter-width);
  }

  .masonry-item {
    // Avoid breaking the item across columns.
    // `display: inline-block` gives us the desired behavior more often than
    // `break-inside: avoid`, which is not well supported as of IE11 and FF31.
    display: inline-block;
    width: 100%;
  }

  .masonry-rules(@size) {
    .masonry-@{size}-1  { .masonry(1);  }
    .masonry-@{size}-2  { .masonry(2);  }
    .masonry-@{size}-3  { .masonry(3);  }
    .masonry-@{size}-4  { .masonry(4);  }
    .masonry-@{size}-5  { .masonry(5);  }
    .masonry-@{size}-6  { .masonry(6);  }
    .masonry-@{size}-7  { .masonry(7);  }
    .masonry-@{size}-8  { .masonry(8);  }
    .masonry-@{size}-9  { .masonry(9);  }
    .masonry-@{size}-10 { .masonry(10); }
    .masonry-@{size}-11 { .masonry(11); }
    .masonry-@{size}-12 { .masonry(12); }
  }

  .masonry-rules(xs);
  @media (min-width: @screen-sm-min) { .masonry-rules(sm) }
  @media (min-width: @screen-md-min) { .masonry-rules(md) }
  @media (min-width: @screen-lg-min) { .masonry-rules(lg) }
}

.no-csscolumns {
  .masonry(@column-count) {
    margin-left: -@grid-gutter-width;

    .masonry-item {
      width: (100% / @column-count);
      float: left;
      padding-left: @grid-gutter-width;
    }
  }


  .masonry-rules(@size) {
    .masonry-@{size}-1  { .masonry(1);  }
    .masonry-@{size}-2  { .masonry(2);  }
    .masonry-@{size}-3  { .masonry(3);  }
    .masonry-@{size}-4  { .masonry(4);  }
    .masonry-@{size}-5  { .masonry(5);  }
    .masonry-@{size}-6  { .masonry(6);  }
    .masonry-@{size}-7  { .masonry(7);  }
    .masonry-@{size}-8  { .masonry(8);  }
    .masonry-@{size}-9  { .masonry(9);  }
    .masonry-@{size}-10 { .masonry(10); }
    .masonry-@{size}-11 { .masonry(11); }
    .masonry-@{size}-12 { .masonry(12); }
  }

  .masonry-rules(xs);
  @media (min-width: @screen-sm-min) { .masonry-rules(sm) }
  @media (min-width: @screen-md-min) { .masonry-rules(md) }
  @media (min-width: @screen-lg-min) { .masonry-rules(lg) }
}
