.nav {
    font-size: 16px;
    font-weight: normal;
}

@border-color: #ccc;

// Tabbed panels
.nav-boxed-tabs {
    &.tabs {
        position: relative;
        a, button {
            height: 100%;
            display: block;
            font-size: 1rem;
            line-height: 1.2;
            text-align: center;
        }
        &:not('.tabs--js') {
            overflow-x: auto;
        }
        .tabs__primary {
            display: flex;
            white-space: nowrap;
            & > li {
                flex-grow: 1;
                &.tab {
                    padding-top: .5rem;
                    transition: all .2s;
                    &.active {
                        padding-top: 0;
                    }
                }
                &.more.active {
                    button {
                        color: #333;
                        font-weight: 700;
                        background-color: #fff;
                    }
                }
            }
        }
        .tabs__secondary {
            display: none;
            position: absolute;
            top: 100%;
            right: 0;
            z-index: 100;
            padding: 0;
            background-color: #fff;
            box-shadow: 0px 0px 2px 1px rgba(87,87,87,0.5);
            & > li {
                border-bottom: 1px solid #eee;
                &.active {
                    font-weight: 700;

                    a{
                        color: #333;
                        background-color: #fff;
                        &:hover {
                            background: #fff;
                        }
                    }
                }
                a {
                    padding: .5rem 1rem;
                    color: #666;
                    transition: color .25s;
                    background-color:#eee;
                    &:hover {
                        background-color: #ddd;
                        text-decoration: none;
                    }
                    &:focus {
                        text-decoration: none;
                    }
                }
            }
        }
        &.tabs--show-secondary {
            .tabs__secondary {
                display: block;
            }
        }
    }
    // text and bg color for inactive tabs
    @color: #666;
    @bg: #eee;

    // text and bg color for inactive & hovered tabs
    @hover-color: #333;
    @hover-bg: #ddd;

    // text and bg color for active tabs
    @active-color: #333;
    @active-bg: white;

    > li {
        float: left;

        &:first-child > a {
            border-top-left-radius: @panel-border-radius;
        }
        &:last-child > a {
            border-top-right-radius: @panel-border-radius;
        }

        // Make adjacent tabs have touching borders collapse together
        // merge the borders of adjacent tabs
        margin-right: -1px;

        // place bottom borders of tabs on top of top border of .tab-content
        margin-bottom: -1px;

        > a, > button {
            color: @color;
            background-color: @bg;
            border: 1px solid @border-color;
            height: 100%;
            .transition-property(color, background-color, border-color;);
            .transition-duration(100ms);
            .transition-timing-function(ease-out);

            &:hover {
                background-color: @hover-bg;
            }
        }

        &.active > a {
            &,
            &:hover,
            &:focus {
                color: @active-color;
                border: 1px solid @border-color;
                background-color: @active-bg;
                border-bottom-color: @active-bg;
                outline: none;
            }
        }
    }

    & + .tab-content {
        background-color: @active-bg;
        border: 1px solid @border-color;
        .border-bottom-radius(@panel-border-radius);

        .box-shadow(0 1px 1px 0 rgba(0,0,0,.05));

        padding: 15px; // match padding BS gives to panels
        margin-bottom: @line-height-computed;

        // panes with little or no content are sized reasonably & consistently
        min-height: 20em;
    }


    // .nav-stacked is Bootstrap's vertical layout for tabs and buttons.
    &.nav-stacked {
        padding-right: 0;
    }
    &.nav-stacked > li {
        float: none;
    }
    &.nav-stacked > li + li {
        /* override BS margin */
        margin-top: 0;
    }
    .nav-stacked > li a {
        display: block;
    }
}

// Stacked+boxed tabs used in Orchestration Actions
.nav-boxed-tabs.nav-stacked {
    > li:last-child > a {
        border-top-right-radius: 0;
        border-bottom-left-radius: @panel-border-radius;
    }

    // With our use of vertical tabs, the effect of the active tab being
    // continuous with its panel is broken.  These rules fix it.
    > li.active a {
        z-index: 1000;  // Below our dialog which is 1050
        border-right-color: transparent;
        border-bottom-color: @border-color;
        // Higher z-index makes this tab overlay the next one, so move it up 1px
        // to bring back the next one's top border.
    //  position: relative;
    //  top: -1px;
    }
}


// Global Navigation

#header {
    // to center nav
    text-align: center;
}

#footer {
    div {
        &:hover {
            filter: drop-shadow(0 0 5px rgba(255,255,255,0.5));
        }
    }
}

.nav-header-tabs {
    @tab-color: @header-bg; // tab color
    @reversed-text-color: white;
    @dropdown-text-color: #333;
    @tab-lightened-color: lighten(mix(@tab-color, @body-bg, 90%), 2%);

    // text and bg color for hovered links
    @hover-color: #333;
    @hover-bg: #ddd;

    border: none;

    //background-color: @tab-color;
    color: @reversed-text-color;
    .dropdown {
      color: @dropdown-text-color;
    }

    padding-left: 0;


    // Nav Item Base Styles (top items in navbar)
    &.nav {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .nav-right {
        display: flex;
        flex-basis: 50%;
        justify-content: flex-end;
    }
    .nav-left {
        display: flex;
        flex-basis: 50%;
        justify-content: flex-start;
    }
    li {
        margin: 0;
        padding: 0;
        display: flex;
        align-items: center;
        position: unset;

        ul { // second level/drop down
          display: block;

          // Initially these are hidden and scaled down. On hover, a nice
          // scaling/fading in transition animates them.
          opacity: 0;
          visibility: hidden;
          .transition(all 0.02s);
          position: absolute;
          top: 2.5rem;

          // Submenus should be high, but below our modals which are 1050
          // 1000 is not high enough: table blocker on Servers page is higher,
          // as are active tabs on Orch Actions: the Servers and Catalog
          // submenus were underneath it. 1020 fixes both.
          z-index: 1020;
          background-color: @panel-bg;
          background-image: linear-gradient(to bottom, @panel-bg, darken(@panel-bg, 3%));
          overflow: hidden;
          border-bottom: 1px solid @border-color;

          // Horizontally align submenu icons and text
          .fa,
          [class^=icon] {
            width: 22px;
          }

          // Style headings and links everywhere like links in submenu LIs
          li {
            .accordion > .panel {
              margin-bottom: 0;
            }

            h4 > a {
              margin-left: .5em;
            }

            // Match heading left padding with normal links
            h4.no-link { padding-left: 20px; }
          }

          li.depth-5 > a {
            padding: 3px 10px;
          }
        }

        // Match vertical position of submenu under the gravatar with other submenus

        // Since js cannot trigger the pseudo :hover event, also support the "open" class here.
        &.open ul {
          display: block;
          opacity: 1;
          visibility: visible;
          .transition(all .02s);
        }

        // Top-level nav item styles
        > a {
            text-decoration: none;

            color: @reversed-text-color;

            .transition-property(background-color;);
            .transition-duration(100ms);
            .transition-timing-function(ease-out);
        }

        // Nav Item Interaction States

        // State 1: default state
        > a {
            background-color: transparent;
        }

        // State 2: interacting/choosing new page
        > a:hover,
        > a:focus,
        // when dropdowns are open...
        &.open > a,
        &.open > a:focus {
            outline: none;
        }

        // State 3: active/current page
        &.active > a,
        &.active > a:focus {
            background-color: lighten(@tab-lightened-color, 7%);
            outline: none;
        }

        .badge {
          // otherwise badges are too low
          vertical-align: middle;
        }
    }

    li[data-topnav="dashboard"] {
        color: transparent;
        a {
            background-image: url(/static/logos/cb-cloud-white.svg);
            background-size: contain;
            background-position: center center;
            background-repeat: no-repeat;
            width: 2.5rem;
            height: 2.5rem;
        }
    }
    li[data-topnav="ccso"] {
        color: transparent;
        a {
            background-image: url(/static/logos/CloudBolt_CCSO-logo.svg);
            background-size: contain;
            background-position: center center;
            background-repeat: no-repeat;
            width: 1rem;
        }
    }

    @width-per-level: .5em;

    li.depth-1  { padding-left: 0 * @width-per-level; }
    li.depth-2  { padding-left: 1 * @width-per-level; }
    li.depth-3  { padding-left: 2 * @width-per-level; }
    li.depth-4  { padding-left: 3 * @width-per-level; }
    li.depth-5  { padding-left: 4 * @width-per-level; }
    li.depth-6  { padding-left: 5 * @width-per-level; }
    li.depth-7  { padding-left: 6 * @width-per-level; }
    li.depth-8  { padding-left: 7 * @width-per-level; }
    li.depth-9  { padding-left: 8 * @width-per-level; }
    li.depth-10 { padding-left: 9 * @width-per-level; }
}

.submenu {
    width: 100vw;
    min-height: 50vh;
    border-radius: 0;
    display: flex!important;
    padding: 1rem;
    .dropdown-menu__sidebar {
        min-width: 12rem;
        max-width: 20rem;
        border-right: 1px solid #000 !important;
        padding: .5rem 1.25rem .5rem .5rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .sidebar__info {
            color: #000;
            font-size: .9rem;
            line-height: 21.5px;
            .info {
                max-height: 18rem;
                overflow-y: auto;
            }
            .info-btns {
                margin-top: 1rem;
            }
        }
    }
    .dropdown-menu__actions {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .quick-actions {
            margin-top: 1rem;
            .panel-title {
                margin-bottom: .5rem;
            }
        }
        .cb-btn {
            margin-bottom: .5rem;
        }
        .bold {
            font-weight: 700;
        }
    }
    .dropdown-menu__content {
        width: 100%;
        padding: .5rem 1rem;
        .content {
            font-size: 1rem!important;
            max-height: 70vh;
            overflow-y: auto;
            .img-container {
                margin-bottom: 1rem;
                img {
                    width: 3rem!important;
                    height: auto!important;
                }
            }
            a {
                color: #3959a2!important;
                max-width: 20rem;
                text-decoration: none;
                line-height: 1.9;
                display: inline-block;
                max-width: 40rem;
                &:hover {
                  text-decoration: underline;
                }
            }
            &#resources-content,
            &#catalog-content,
            &#bookmarks-content,
            &#cart-content,
            &#library-content {
                display: flex;
                flex-direction: column;
                flex-wrap: wrap;
                width: 100%;
                max-height: 25rem;
                a {
                    padding: 0 .75rem;
                }
            }
            &#reports-content, &#admin-content, &#support-content {
                display: flex;
                flex-direction: row;
                align-items: flex-start;
                height: 100%;
                .section {
                    flex-basis: 33.33%;
                    padding: .5rem 1rem;
                    display: flex;
                    flex-direction: column;
                    border-right: 1px solid rgba(0,0,0, 0.25);
                    height: 100%;
                    .section__content {
                        display: flex;
                        flex-direction: column;
                        margin-bottom: 1rem;
                        max-width: 25rem;
                    }
                    &:last-of-type {
                        border-right: none;
                    }
                    h5 {
                        font-weight: 700;
                    }
                    a {
                        padding: 0 .75rem!important;
                        width: 100%;
                        overflow-x: hidden;
                        text-overflow: ellipsis;
                    }
                }
            }
            &#cart-content {
                overflow-y: auto;
                a {
                    width: 100%;
                }
            }
            &#groups-content {
                overflow-y: auto;
                table {
                    color: #000;
                    tbody {
                        tr {
                            font-size: 1rem;
                            &:nth-of-type(odd) {
                                background-color: #F7F7F7!important;
                                transition: all .5s;
                            }
                            &:hover {
                                background-color: #e0e0e0 !important;
                            }
                            td {
                                border-top-color: #F7F7F7!important;
                                a {
                                    &.depth{
                                        &-2 {
                                            padding-left: 1.25rem;
                                        }
                                        &-3 {
                                            padding-left: 2.25rem;
                                        }
                                        &-4 {
                                            padding-left: 3.25rem;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
