// Ace code editor
// html element is here to make this more specific than the default .ace_editor rule
html .ace_editor {
  position: relative;
  border: 1px solid @table-border-color;
  border-radius: @panel-border-radius;

  height: 48em;

  &.ace-one-line {
    line-height: 26px; // essentially cursor height
    height: 34px;
    min-height: 34px;
  }
  &.ace-sm {
    height: 24em;
  }
  &.ace-md {
    height: 48em;
  }
  &.ace-lg {
    height: 60em;
  }
}

.editor-wrap {
  // Toolbar for lang & snippet controls
  .btn-toolbar {
    padding: 0;
    margin: 0;
  }

  .snippets {
    .selectize-control {
      display: inline-block;
      min-width: 10em;
    }
  }
}
