.btn {
  text-align: left;
}

// Buttons styled with bootstrap-toggle require centered text
.btn.toggle,
.toggle-group .btn,
.btn-block {
  text-align: center;
}

// use to wrap a disabled button to add a tooltip or
// other element that needs to receive events.
.btn-wrapper {
  display: inline-block;
  border-bottom: none;
}

// Remove native browser style for focused elements (blue outline in Chrome).
// We provide custom styles for :focus below.
.btn:focus {
  outline: 0;
}

// Inspired by https://gist.github.com/claviska/6117193
.better-btn(@color, @background, @text-shadow: none) {
  color: @color;
  border-color: darken(@background, 10%);
  border-bottom-color: darken(@background, 20%);
  // Subtle lighter outline
  .box-shadow(inset 0 0 2px rgba(255, 255, 255, 0.8));

  #gradient > .vertical(lighten(@background, 5%), darken(@background, 5%), 0%, 100%);

  &:hover,
  &:focus,
  &:active,
  &.active {
    #gradient > .vertical(darken(@background, 0), darken(@background, 10%), 0%, 100%);
    border-color: darken(@background, 20%);
    color: @color;
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus,
    &:active,
    &.active {
      color: lighten(@color, 50%);
      background-color: @background;
      border-color: darken(@background, 5%);
    }
  }
}

// More pronounced gradients
.better-btn-strong(@color, @background, @text-shadow: none) {
  color: @color;
  #gradient > .vertical(lighten(@background, 10%), darken(@background, 10%), 0%, 100%);
  border-color: darken(@background, 5%);
  border-bottom-color: darken(@background, 25%);
  text-shadow: @text-shadow;
  // Subtle lighter outline
  .box-shadow(inset 0 0 2px rgba(255, 255, 255, 0.5));

  &:hover,
  &:focus,
  &:active,
  &.active {
    #gradient > .vertical(darken(@background, 0), darken(@background, 15%), 0%, 100%);
    border-color: darken(@background, 20%);
    color: @color;
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus,
    &:active,
    &.active {
      color: lighten(@color, 50%);
      background-color: @background;
      border-color: darken(@background, 8%);
    }
  }
}

.btn-default {
  border-color: #cbcbcb;
  background-color: #e2e2e2;
  &:hover,
  &:focus {
    .collapsed-indicator {
      color: #333;
    }
  }
  span.infotip {
    color: #4b4a4a;
  }
  &.disabled,
  &.disabled:hover {
    color: #cbcbcb;
    background-color: #f7f7f7;
  }

  // Vertical button groups need a different treatment, e.g. server details and
  // Add RH dialog
  .btn-group-vertical & {
    #gradient > .horizontal-three-colors(
      lighten(@btn-default-bg, 5%),
      lighten(@btn-default-bg, 7%),
      50%,
      lighten(@btn-default-bg, 5%)
    );
  }

  &:hover,
  &:focus,
  &:active,
  &.active {
    .box-shadow(inset 0 0 8px rgba(0, 0, 0, 0.05));

    .btn-group-vertical & {
      #gradient > .horizontal-three-colors(
        darken(@btn-default-bg, 0%),
        darken(@btn-default-bg, 0%),
        50%,
        darken(@btn-default-bg, 0%)
      );
    }
  }
}

.btn-primary {
  border: 0.5px solid #17659d;
  background: #335b83;
  &:hover {
    background: #0d65a2;
    box-shadow: inset 0px 0px 4px rgba(0, 0, 0, 0.56);
    text-decoration: none;
  }
  &:focus {
    text-decoration: none;
  }
  &:active {
    background: #0d65a2;
    border: 0.5px solid #103f61;
    box-shadow: inset 0px 0px 2px rgba(0, 0, 0, 0.56);
    text-decoration: none;
  }
}

.cb-btn {
  display: flex;
  padding: 0.3rem;
  text-align: center;
  border-radius: 10px;
  box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.56);
  font-size: 12px;
  font-family: "Helvetica", sans-serif;
  line-height: 14px;
  font-weight: 700;
  width: 100%;
  height: 2rem;
  justify-content: center;
  align-items: center;
  transition: all 0.25s;
  &-primary {
    border: 0.5px solid #17659d;
    background: #335b83;
    color: #fff;
    &:hover {
      background: #0d65a2;
      box-shadow: inset 0px 0px 4px rgba(0, 0, 0, 0.56);
      color: #fff;
      text-decoration: none;
    }
    &:focus {
      text-decoration: none;
      color: #fff;
    }
    &:active {
      background: #0d65a2;
      border: 0.5px solid #103f61;
      box-shadow: inset 0px 0px 2px rgba(0, 0, 0, 0.56);
      text-decoration: none;
      color: #fff;
    }
  }
  &-secondary {
    background: #f4f7ff;
    border: 1px solid rgba(48, 87, 126, 0.3);
    box-shadow: inset 0px 0px 2px #000000;
    color: #0051a0;
    &:hover {
      background: #dbe0f0;
      box-shadow: inset 0px 0px 2px #000000;
      text-decoration: none;
      color: #0051a0;
    }
    &:focus {
      text-decoration: none;
      color: #0051a0;
    }
    &:active {
      background: #c6ccde;
      color: #0051a0;
    }
  }
  &-link {
    box-shadow: none;
    color: #0051a0;
    text-decoration: underline;
    border: none;
    background: transparent;
    &:hover {
      background: rgba(219, 224, 240, 0.5);
      box-shadow: inset 0px 0px 2px rgba(0, 0, 0, 0.2);
      text-decoration: underline;
    }
    &:focus {
      border: 1px solid #727272;
      border-radius: 12px;
      text-decoration: underline;
    }
    &:active {
      background: rgba(198, 204, 222, 0.5);
      box-shadow: inset 0px 0px 2px rgba(0, 0, 0, 0.3);
      text-decoration: underline;
    }
  }
  &-danger {
    color: #fff;
    background: #d9534f;
    border: 1px solid rgba(217, 83, 79, 0.3);
    &:hover {
      background: #c9302c;
      box-shadow: inset 0px 0px 2px rgba(0, 0, 0, 0.2);
      text-decoration: none;
    }
    &:active {
      background: #c9302c;
    }
  }
}

// data table pagination buttons are selected very specifically in jquery datatable plugin css
// So to override them, we need to be just as specific as the datatable plugin and load this
// later in the page (which we do by default). There are also a lot of !important rules in here
// to override the datatable plugin's !important rules.
.dataTables_wrapper .dataTables_paginate .paginate_button {
  .btn();
  .btn-default();
  &.current {
    .btn-primary();
    background: #335b83 !important;
    color: #fff !important;
    &:hover {
      background: #0d65a2 !important;
      color: #fff !important;
    }
  }
  &.disabled {
    cursor: not-allowed !important;
    opacity: 0.65 !important;
    box-shadow: none !important;
    color: #cbcbcb !important;
    background-color: #f7f7f7 !important;
    border-color: #ccc !important;
    &:hover {
      color: #cbcbcb !important;
    }
  }
  &:hover {
    background-image: none;
    color: #333 !important;
  }
  &:focus {
    outline: 0;
  }
}
