
label {
  font-weight: normal;
}

.form-control,
.form-group {
  label {
    font-weight: normal;

    &.required,
    &.requiredField {
      font-weight: bold;
    }
  }
}

// The box-shadow property from BS's .form-control sometimes shows up
// (unintendedly) on these elements, so we remove the box-shadow.
input[type="checkbox"],
input[type="radio"] {
  .box-shadow(none);
  height: inherit;/* avoid the BS 34px height */
}

fieldset {
  margin-bottom: .5em;
}

// We use fieldsets sometimes instead of div.form-group, so just apply
// the same styles to it (i.e. make them behave like rows)
.order_forms fieldset {
  .form-group;
  // form-group sets a larger margin but here we want compact layout.
  // Also, 8px or less leads to broken input-group add-ons (e.g. units str).
  margin-bottom: 9px;
}


// Similar to BS .radio-inline and .checkbox-inline, this can be
// applied to our widgets such as RadioList to lay them out horizontally
ul.list-inline li {
  display: inline-block;
}

/*** Formsets and the jquery.formset plugin ***/

// the row containing 'add' and 'remove all' links 
tr.formset-controls {
  border-top: 1px solid #d0d0d0;
  background-color: #eee;
}

.blank-form-template {
  display: none;
}


// These formset links no longer have a void href attr, so browsers treat them
// like placeholders; need to reinstate the cursor style.
a.add-form,
a.unremove,
a.delete-form,
a.delete-all-forms {
  cursor: pointer;
}

// field-level help text as defined by form field help_text
.help-block {
  font-size: 85%;
  font-style: italic;
  // a wee bit of separation from the input field looks better, as does a tiny amount of indentation.
  margin: .15em;

  // some help text looks best not italicized
  code {
    font-size: 90%;
    font-style: normal;
    background-color: #eee;
  }
}

input.large-width{
  width: 30em;
}

// Similar to Bootstrap's `.form-horizontal`, but easier to use when Django is
// used to render inputs and it's not simple to add a `.form-control` class.
// Class should be applied to a table element, with labels in THs in the first
// column, and controls in the TDs in the second column.
.form-table {
  > thead,
  > tbody,
  > tfoot {
    > tr {
      > th,
      > td {
        padding: @table-cell-padding;
        line-height: @line-height-base;
        vertical-align: top;
      }
    }
  }

  // Consistent vertical alignment of labels, radios, and checkboxes
  .control-label,
  .radio,
  .checkbox,
  .radio-inline,
  .checkbox-inline {
    margin-top: 0;
    margin-bottom: 0;
    padding-top: (@padding-base-vertical + 1); // Default padding plus a border
  }

  // If used as table within a cell of another table, emulate the effect of
  // removing padding from the edges of the this table's outside-facing edges
  // so that their contents align with the contents of the parent table.
  // Usage example: `edit-preconfig-dialog.html`
  th > &,
  td > & {
    margin: -@table-cell-padding !important;
  }

  // Selectize elements aren't naturally sized according to their content, and
  // are too small to contain their typical content. We increase the min-width
  // of a selectize element to better accomodate typical use.
  .selectize-input {
    min-width: 15rem;
  }
}


// RuntimeToggleWidget needs some tweaks to make it really shine
.runtime-group {
  // lay out horizontally
  & > div {
    display: inline-block;
  }

  // Needs smaller text. Using bootstrap toggle's data-size=small works but
  // makes the button height smaller than the neighboring widget which looks
  // clunky.
  .toggle-group label {
    font-size: 90%;
  }

  .toggle.btn {
    margin-right: 2em;
  }
}


// Fieldsets with legends and borders to group their contents
fieldset.bordered {
  padding: 8px;
  border: 1px solid #d0d0d0;
  border-radius: 8px;

  legend {
    border: none;
    width: auto;
    margin: 4px; // fieldset padding is enough
    font-size: 100%;
  }
}


// Override .form-control's width of 100% which checkboxes don't need.
// This prevents a 100% wide blue highlight box when checkbox is focused.
input[type="checkbox"].form-control {
    width: auto !important;
}


/*
 * Styles for editing propery values inline instead of via a dialog + page reload.
 * See c2.inlineEdits.js
 */

.inline-editable {
  cursor: pointer;
}

.inline-edit-link {
  margin-left: 8px;
}

.inline-editable-form {
  position: absolute;
  // Higher than surrounding elements such as .panel
  z-index: 1000;

  // Labels are already in the page
  label { display: none; }

  // Undo the Bootstrap grid columns
  .controls {
  }

  .btn-toolbar {
    display: inline-block;

    // Position/align with selectize & input fields
    margin: 2px;
    position:relative;
    top: -2px;

    // This A has no href so add styles to make it behave clickable
    [data-btn-cancel] {
      padding: 4px;
      cursor: pointer;
    }
  }

  .selectize-input {
    // Very specific rule to keep these from becoming too narrow
    // when user hits delete key to clear existing choice.
    min-width: 200px;
  }
}

/* Draw a rounded box with shadow, absolutely positioned */
.inline-dialog {
  display: inline-block;
  position: absolute;
  padding: 1em;
  background-color: #eee;
  border: 1px solid #ddd;
  border-radius: @border-radius-small;
  min-width: 400px;

  // Higher than surrounding elements such as .panel
  z-index: 1000;

  .box-shadow(0 7px 25px -11px rgba(0,0,0,0.5));
}


label.child-field {
  float: left;
  margin-right: 1em;
}

.group-daterange {
  display: flex;
}

.filter-row {
  display: flex;
  flex-direction: row;
}

li.inline-field {
  margin-left: 1%;
}

.daterange-group {
  margin-bottom: 1%;
}

.daterange-group input:nth-of-type(1) {
  border-radius: 3px 0 0 3px !important;
}

.daterange-group input:last-child {
  border-radius: 0 3px 3px 0 !important;
}

.daterange-error {
  display: none;
  margin-top: -1%;
}

.daterange-error-active {
  display: inline;
  color: red;
}

.daterange-group-error {
  border-color: red;
  color: red;
}
