.quick-setup {
  padding: 1rem;

  #quick-setup-panel {
    // A line-length for comfortable reading. Also this centers the EULA nicely;
    // needed since we don't have control over its content (direct from MS Word).
    width: 45rem;
    margin: auto;

    > .panel-body {
      // More consistent height from step to step
      min-height: 25rem;
    }

    .panel-footer > .btn-toolbar {
      text-align: center;  // vertically center the pager
      margin-bottom: 0;
    }

    // a BS style, but override our default label width
    label.checkbox-inline {
      width: auto;
    }

    // Layout the 'Setup Completed' page: definition terms & defs 50/50
    .dl-horizontal {
        dt {
            // override Bootstrap width
            width: 48%;
        }
        dd {
            // override global CB styles
            margin-left: auto;
            max-width: 48%;
        }
    }

    .wizard-pagination {
      .pagination-lg();
    }
  }

  img {
    display: block;
    margin: auto;
    margin-bottom: 1rem;
  }

  // Give the primary QS heading a smaller style without changing its
  // semantic/hierarchical level.
  h1 {
    text-align: center;
    .h3();
  }
}


// Keep the EULA panel at a comfortable size (not too big)
.quick-setup-step-0 #quick-setup-panel > .panel-body {
  height: 30rem;
  overflow-y: auto;
}


.eula {
  p, h1, h2, h3, small {  // reset styles for EULA
    margin: 0;
    padding: 0;

    font-size: 1rem;
    line-height: 1.5rem;
    margin-bottom: 1.5rem;

    font-family: @font-family-serif;
  }

  h1, h2, h3 {
    font-weight: bold;
  }

  h1 {
    font-size: 2rem;
    line-height: 3rem;
  }

  h2 {
    font-size: 1.5rem;
    margin-top: 3rem;
  }

  h3 {
    margin-bottom: 0;
  }

  small {
    font-size: 0.75rem;
  }
}
