// Select all & none buttons
.selectize-toolbar {
  // override Selectize rule
  margin: 0 !important;

  float: right;

  // Let the toolbars position to the right of the input
  .inline-selectize-toolbars & {
      float: left;
      display: inline-block;
  }
}

// Let the toolbars position to the right of the input
.inline-selectize-toolbars .selectize-input {
  width: 90% !important;
  float: left;
}

.selectize-control.plugin-remove_button {
  .selectize-input {
    [data-value] {
      position: relative;
      // space selected values apart a bit
      margin-bottom: 3px !important;
      padding-right: 24px !important;
      .btn;
      .btn-default;
      .btn-xs;
      border: none;
      // Button bg color only on hover; this enables our overflow-shadow effect
      background: none;

      & .remove {
        z-index: 1; /* fixes ie bug (see #392) */
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        width: 17px;
        text-align: center;
        font-weight: normal;
        font-size: 1em;
        // Prevent link color
        color: inherit;
        text-decoration: none;
        vertical-align: middle;
        display: inline-block;
        padding: @selectize-padding-item-y 0 0 0;
        border-left: 1px solid @selectize-color-item-border;
        .selectize-border-radius(0 2px 2px 0);
        .selectize-box-sizing(border-box);

        &:hover {
          background: rgba(0,0,0,0.05);
        }
      }

      &.active .remove {
        border-left-color: @selectize-color-item-active-border;
      }
    }

    .disabled [data-value] {
      .remove {
        border-left-color: lighten(
          desaturate(@selectize-color-item-border, 100%),
          @selectize-lighten-disabled-item-border);

        &:hover {
          background: none;
        }
      }
    }
  }
}


// Give SelectizeMultiple a dropdown hint: the same triangle used on
// SelectizeSelect (in selectize/selectize.less)
.selectize-control.multi .selectize-input {
  &:after {
    content: ' ';
    display: block;
    position: absolute;
    top: 50%;
    right: @selectize-arrow-offset;
    margin-top: round(-@selectize-arrow-size / 2);
    width: 0;
    height: 0;
    border-style: solid;
    border-width: @selectize-arrow-size @selectize-arrow-size 0 @selectize-arrow-size;
    border-color: @selectize-arrow-color transparent transparent transparent;
  }
  &.dropdown-active:after {
    margin-top: @selectize-arrow-size * -0.8;
    border-width: 0 @selectize-arrow-size @selectize-arrow-size @selectize-arrow-size;
    border-color: transparent transparent @selectize-arrow-color transparent;
  }
}
