code {
  color: @text-color;
}

.pre-unboxed {
  padding: 0;
  background-color: transparent;
  border: none;
}

.pre-scrollable {
  overflow: auto;
}

.lines-40 {
  max-height: 40em;
}

pre, code {
  font-size: @font-size-base;
}

pre > code.hljs {
  // reset hljs's slightly different bg color to ours
  background-color: @pre-bg;
}
