/*
 #f2f1f0 - lt grey
 #77787b - md grey: headings
 #7CA0C7 - md blue: table cells
 #447fc3 - dk blue
 #fcf6c2 - lt yellow: row highlight on hover
 #fce67e - lt orange: map, hover
 #f5c031 - md orange: map
 #f18216 - dk orange
 */
html,
body {
  margin: 0;
  padding: 0;
}
/* darker emphasized headings/parts of headings */
h1 em,
h2 em,
h3 em,
h4 em,
h5 em,
h6 em {
  color: #333;
}
li {
  list-style: none;
}
dl {
  margin: 0;
  overflow: auto;
}
dt {
  font-weight: bold;
  clear: both;
  float: left;
  /* Ensure consistent width. Looks good in most places, but override as
     * needed: too narrow causes wrapping, too wide separates dt & dd too much. */
  min-width: 20%;
  margin-right: 0.5em;
}
dt.nowrap {
  width: auto;
}
dt.medium-tall {
  min-height: 4em;
}
.orderItemLists dt {
  /* 20% causes some DTs with long content to overlap DD */
  width: 30%;
}
/* Bring overview tab DTs closer to their DD values */
#tab-overview dt {
  width: 16%;
}
dd {
  padding-bottom: 1px;
  /* Just enough so .tiptext border isn't hidden */
}
dd p {
  margin: 0.3em 0;
}
.one-third {
  width: 33%;
}
ul.reduced,
ul.reduced li {
  padding: 0;
  margin: 0 !important;
}
dd {
  float: left;
  display: inline;
  margin: 0;
  max-width: 70%;
  /* must be updated whenever width of <dt> is modified */
}
/* RH list details column */
td.details {
  width: 180px;
}
td.details dt,
td.details dd {
  display: inline-block;
  float: none;
  width: auto;
}
/* lists in cloudbolt-palette */
dl.sc {
  background-color: #C5DEFC !important;
  border: 1px solid #9ebbdb !important;
  background: -moz-linear-gradient(top, #e2edf7 0%, #c4dbef 100%) !important;
  /* FF3.6+ */
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #e2edf7), color-stop(100%, #c4dbef)) !important;
  /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(top, #e2edf7 0%, #c4dbef 100%) !important;
  /* Chrome10+,Safari5.1+ */
  background: -o-linear-gradient(top, #e2edf7 0%, #c4dbef 100%) !important;
  /* Opera 11.10+ */
  background: -ms-linear-gradient(top, #e2edf7 0%, #c4dbef 100%) !important;
  /* IE10+ */
  background: linear-gradient(top, #e2edf7 0%, #c4dbef 100%) !important;
  /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#e2edf7', endColorstr='#c4dbef', GradientType=0) !important;
  /* IE6-9 */
}
dl.sc dt.first {
  margin-top: 0;
}
dl.sc dt {
  background-color: #b8d1ec;
  border-right: 1px solid #9ebbdb;
  border-bottom: 1px solid #9ebbdb;
  margin: 1px 1em 0 0;
  padding: 2px 1em;
  line-height: 1.5em;
}
dl.sc dt:last {
  border-bottom: none;
}
dl dt.head {
  width: 100% !important;
  text-align: center;
  margin: 0;
  padding: 0.25em 0;
  border: none;
