/*
 * Dialogs
 *
 * Most styles for our modal come from Bootstrap 3.
 */
.modal.fade .modal-dialog {
  transform: translateY(0) !important;
  min-width: 650px !important;
}
.modal.fade.in,
.modal.fade.show {
  background: rgba(0, 0, 0, 0.5) !important;
  opacity: 1 !important;
}
.modal-full {
  width: calc(100vw - 6rem);
}
.modal-backdrop {
  background: rgba(0, 0, 0, 0.5) !important;
}
.danger .modal-content {
  border: solid #d9534f 4px;
}
.modal-content .modal-body {
  min-height: 100%;
  height: 100%;
  display: block;
}
.modal-content .modal-body form {
  border: none;
}
.modal-content .modal-body p {
  font-size: 14px;
  font-family: open_sans, "Helvetica Neue", Helvetica, Arial, sans-serif;
}
.modal-header {
  margin: 0 1rem;
  padding: 15px 0;
  display: flex;
  align-items: center !important;
}
.modal-header .modal-title {
  width: 97%;
  font-size: 18px;
  font-weight: 400;
  font-family: open_sans, "Helvetica Neue", Helvetica, Arial, sans-serif;
}
.modal-header .close {
  margin: 0;
  opacity: 0.2 !important;
}
.modal-header .close:hover {
  opacity: 0.5 !important;
}
.modal-footer {
  margin: 0 1rem;
  display: flex;
  justify-content: flex-end;
}
.modal-footer .cb-btn {
  margin-left: 0.75rem;
  min-width: 6rem;
  max-width: 8rem;
}
.modal-footer .btn-toolbar {
  display: flex;
  flex-basis: 100%;
  justify-content: flex-end;
  align-items: center;
}
.modal-dialog textarea {
  resize: vertical;
}
/* A companion to Bootstrap's `.container`, `.full-container` is full-width and
 * takes no rows or columns. */
.full-container {
  padding: 1em;
}
.full-container-no-top {
  padding-top: 0;
}
.full-container-no-bottom {
  padding-bottom: 0;
}
.fluid-container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  max-width: 110rem;
}
