.vbox {
  display: table;
  table-layout: fixed;
  width: 100%;
  height: 100%;
}
.vbox > .box-shrink {
  display: table-row;
  height: 1px;
}
.vbox > .box-expand {
  display: table-row;
  height: auto;
}
.dead-center-container {
  display: table;
  width: 100%;
  height: 100%;
}
.dead-center-content {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}
.dead-center-content > * {
  text-align: left;
  display: inline-block;
}
/* Vertical centering of content
 *
 * Usage:
 *     div.center-vertically <- give this a height
 *         div.center-vertically-inner
 *             img.example
 *
 * Used in large blueprint and resource cards to center logo and name
 */
.center-vertically {
  display: table;
  width: 100%;
}
.center-vertically > .center-vertically-inner {
  display: table-cell;
  vertical-align: middle;
}
