/**
 * Spotlight search feature (on Admin page)
 *
 * As user types, matching elements are highlighted in place.
 */
.spotlight-search-bar {
  width: 25em;
  margin: 2em auto;
  position: relative;
}
.spotlight {
  background-color: #fcf6c2;
  border-radius: 2px;
  -webkit-box-shadow: 0 4px 10px -4px rgba(0, 0, 0, 0.3);
  box-shadow: 0 4px 10px -4px rgba(0, 0, 0, 0.3);
}
.spotlight-results {
  position: absolute;
  padding: 1em;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  -webkit-box-shadow: 0 7px 25px -11px rgba(0, 0, 0, 0.5);
  box-shadow: 0 7px 25px -11px rgba(0, 0, 0, 0.5);
  z-index: 500;
  opacity: 0;
  visibility: hidden;
  -webkit-transform-origin: top;
  -moz-transform-origin: top;
  -ms-transform-origin: top;
  transform-origin: top;
  -webkit-transform: scaleY(0.75);
  -moz-transform: scaleY(0.75);
  -ms-transform: scaleY(0.75);
  transform: scaleY(0.75);
  -webkit-transition: all 100ms cubic-bezier(0.55, 0.085, 0.68, 0.53);
  -o-transition: all 100ms cubic-bezier(0.55, 0.085, 0.68, 0.53);
  transition: all 100ms cubic-bezier(0.55, 0.085, 0.68, 0.53);
}
.spotlight-results.active {
  display: block;
  opacity: 1;
  visibility: visible;
  -webkit-transform: scaleY(1);
  -moz-transform: scaleY(1);
  -ms-transform: scaleY(1);
  transform: scaleY(1);
  -webkit-transition: all 150ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
  -o-transition: all 150ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transition: all 150ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.spotlight-results li {
  margin: 0.25em 0;
}
.spotlight-results a {
  padding: 0.25em 0.5em;
  border: 1px solid transparent;
  display: inline-block;
  width: 100%;
}
.spotlight-results a:hover,
.spotlight-results a:focus {
  background-color: #f5f5f5;
  border-radius: 3px;
  border: 1px solid #ddd;
  text-decoration: none;
  outline: none;
}
