/* alerts.less
 *
 * Styles for alerts in a couple of contexts:
 * - Global notifications from Django messages framework and c2.alerts.js module.
 * - Notifications shown in dialogs and other inline contexts (order status on details pg)
 */

// Other variables used in node_modules/bootstrap/less/alerts.less:
// @alert-border-radius: _px;
// @alert-padding: _px;


// Override default Bootstrap alert colors

@alert-success-bg: hsla(100, 60%, 80%, 0.95);
@alert-info-bg: hsla(201, 100%, 84%, 0.95);
@alert-warning-bg: hsla(39, 100%, 90%, 0.95);
@alert-danger-bg: hsla(0, 83%, 83%, 0.95);

.alert {
  // All alerts have a faint border that's a blend of their bg color and black
  border-color: rgba(0, 0, 0, 0.12);
  color: #081109!important;
  font-size: 14px!important;
  &.alert-success {
    background-color: hsla(100,60%,80%,.95)!important;
  }
  &.compact {
    padding: 3px;
  }
}

.alert-dismissable {
  button.close {
    // Less transparent than default to enhance text shadow effect
    opacity: 0.3;
    text-shadow: @inset-dark-text;
    top: 0 !important;
    right: -16px!important;
  }
}


// Global alerts created by c2.alerts.addGlobalAlert client side or server side (Django messages)

#alert-box {
  opacity: 0.95;
  position: absolute;
  right: 0;

  width: 40%; // Keep alerts contained to right side
  z-index: 900;  // Below nav submenus (1000) and the dialog (1050)
}

#alert-box .alert {
  // Normal Bootstrap alert has 20px bottom-margin and 15px padding
  margin: 1em;
  padding-top: 10px;
  padding-bottom: 10px;

  // Scaled down drop shadow
  .box-shadow(0 5px 25px -7px rgba(0, 0, 0, 0.5));
}

// Styles for status icons and text

.error {
    color: red;
}

.warning {
    color: orange;
}
