// Block/cover/shade the nearest relatively positioned ancestor, prevent user interaction.
//
// Make sure that the containing element has ``position: relative;`` as well as ``overflow: hidden`` (if the container has rounded corners).
// Use in combination with a spinner to indicate that something is loading. Example (try placing inside a .modal-content):
//
//   <div class="blocker">
//     <div class="dead-center-container">
//       <div class="dead-center-content">
//         <div class="spinner"></div>
//       </div>
//     </div>
//   </div>

.blocker {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: fade(white, 50%);
  border-radius: 2px;

  // blocker should overlay all other elements
  z-index: @zindex-tooltip + 100;

  @fade-duration: 0.3s;
  // Because a very rapid block/unblock (<200ms) isn't useful to the user, we
  // wait a moment before starting the blocking animation.
  @fade-in-delay: 0.2s;

  /* when not faded in, visibility is hidden to prevent the browser from
   * continuously re-drawing any animated but otherwise invisible content (eg.
   * a spinner). The visibility property transitions immediately from one state
   * to another, so we add a delay to its transition equal to the time it takes
   * to fade out.
   */

  &.fade {
    .opacity(0);
    visibility: hidden;
    @visibility-delay-time: @fade-duration + @fade-in-delay;  // delay hiding until opacity transition is done
    .transition(~"opacity @{fade-duration} 0, visibility 0s @{visibility-delay-time}");

    // don't steal mouse input when element is hidden
    -webkit-pointer-events: none;
            pointer-events: none;
  }
  &.fade.in {
    .opacity(1);
    visibility: visible;
    .transition(~"opacity @{fade-duration} @{fade-in-delay}, visibility 0s @{fade-in-delay}");

    // steal mouse input so input is blocked
    -webkit-pointer-events: auto;
            pointer-events: auto;
  }

  // prevent element from being highlighted and looking ugly
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
}
