/* horizontally stacked bar chart style */
.bar-chart {
  display: inline-block;
  width: 100px; /* can be safely overridden per-use case */
}

.bar {
  display: block;
  float: left;
  text-indent: -9999px;
  height: 100%;
  &.primary { background-color: @brand-primary; }
  &.success { background-color: @brand-success; }
  &.warning { background-color: @brand-warning; }
  &.danger  { background-color: @brand-danger; }
  &.info    { background-color: @brand-info; }
}

/* tool tip styles */
.quota-chart {
  .bar-chart;
}
.quota-tip {
    text-align: left;
    /* bars have higher contrast against white bg */
    background-color: white;
    color: black;
    padding-top: 5px;
    padding-left: 5px;
    padding-right: 5px;
}

.quota-tip .bar {
  height: 1em;
}

/* bar colors */
.used {
  background-color: #4371b2;
}
.delegated {
  background-color: #8bb0d9;
}
.available {
  background-color: #cbe1f4;
}
.exceeded {
  background-color: #c20004;
}

/* vertically stacked bar chart icon style, used only in the Groups page. */
#groupTree .quota-tip,
#groupTree .quota-icon {
    overflow: hidden;
    float: left;
    border-radius: 4px;
    width: 20px;
    height: 20px;
    transform: rotate(270deg);
    margin-right: 4px;
    background-color: #cbe1f4;
}

#groupTree .quota-tip .bar.empty,
#groupTree .quota-icon .bar.empty {
    background-color: #fff;
    border: 1px #ccc solid;
    border-radius: 4px;
}
#groupTree .quota-icon .bar.used { animation-name: grow; animation-duration: .5s; animation-delay: 0s; }
#groupTree .quota-icon .bar.delegated { animation-name: grow; animation-duration: 1s; animation-delay: 0s; }
@keyframes grow { from { width: 0%; } }

