// Class for when users don't have a gravatar
@avatar-bg-color: #ddd;
@avatar-border-color: darken(@avatar-bg-color, 10%);

div.avatar {
    background-image: linear-gradient(to bottom, lighten(@avatar-bg-color, 5%), darken(@avatar-bg-color, 5%));

    border: 1px solid @avatar-border-color;
    // Light border on dark bg
    #header &,
    .cb-inverted & { border: 1px solid rgba(255, 255, 255, 0.15); }

    color: @text-color;
    text-align: center;
    overflow: hidden; // ensure things don't spill out
    display: inline-block;
    margin-right: 5px;

    // This and line-height below vertically center the initials
    vertical-align: middle;

    &.avatar-sm {
        font-size: 11px;
        line-height: 20px;
        height: 22px;
        width: 22px;
        border-radius: 2px;
    }
    &.avatar-med {
        font-size: 16px;
        line-height: 30px;
        height: 32px;
        width: 32px;
        border-radius: 3px;
    }
    &.avatar-lg {
        font-size: 32px;
        line-height: 60px;
        height: 64px;
        width: 64px;
        // Larger avatar gets more radius than the default
        border-radius: 4px;
    }
    &.avatar-xl {
        font-size: 64px;
        line-height: 120px;
        height: 128px;
        width: 128px;
        // Larger avatar gets more radius than the default
        border-radius: 6px;
    }
}

.gravatar {
    // Default radius for inline-size avatars (20px)
    border-radius: 3px;

    // Faint border to give avatar a cleaner outline on dark header
    #header &,
    .cb-inverted & {
        border: 1px solid rgba(255, 255, 255, 0.15);
    }

    // In a link, use margin instead of space character to separate avatar
    // and text. This avoids underline style on the space.
    a > &,
    // And if avatar is adjacent to text that does not want to wrap
    span.nowrap > & {
        margin-right: 6px;
    }

    .gravatar-lg & {
        // Larger avatar gets more radius than the default
        border-radius: 4px;
    }
}

a.rich-gravatar-link {
  &:hover {
    // This prevents ugly underline on whitespace
    text-decoration: none;

    .avatar, // fallback
    img // gravatar img
    {
      // Since there's no underline, indicate hover state by scaling the avatar
      .transition-duration(100ms);
      .transform-origin(center);
      .transform(scale(1.1));
    }
  }
}

li[data-topnav=user] > a {
    // Avatar img breaks the notch; fix by inlining its link
    display: inline;
}
