// blues from darkest to lightest
// https://vis4.net/labs/multihue/#colors=white, 4ba1eb, black|steps=9|bez=0|coL=1
// TODO: this spectrum isn't correct for our logo color, nor are they very
// usable alongside each other due to hue/saturation differences.
@blue-1: #18212d;
@blue-2: #263d56;
@blue-3: #335b83;
@blue-4: #3f7bb2;
@blue-5: #4a9de4;
@blue-6: #8fbcf2;
@blue-7: #e2edfc;
@yellow-1: #fcf6c2;

// LESS vars for colors based on the default portal palette.
// Keep in sync with /portals/models.py
@default-banner-bg-color: #eeeeee; // Same as content_bg_color
@default-topnav-bg-color: #0C4881;
@default-topnav-text-color: #ffffff;
@default-content-bg-color: @default-banner-bg-color;
@default-content-text-color: @text-color;
@default-heading-text-color: @text-color;
@default-tooltip-bg-color: #223c57;
@default-tooltip-text-color: #ffffff;

@header-bg: @blue-3;
@body-bg: @blue-7;
@inset-light-text: 0 -1px 0 rgba(0, 0, 0, 0.3);
@inset-dark-text: 0 1px 0 rgba(255, 255, 255, 0.5);

@btn-default-color: @text-color;
@btn-default-bg: darken(#fff, 8%);
@btn-default-text-shadow: @inset-dark-text;
@btn-default-border: #ccc;

@btn-default-color: @text-color;
@btn-default-bg: darken(#fff, 8%);
@btn-default-text-shadow: @inset-dark-text;
@btn-default-border: #ccc;

@btn-primary-color: #fff;
//@btn-primary-bg: @blue-4;
@btn-primary-text-shadow: @inset-light-text;

@highlight-bg: @yellow-1;

// Standard timings for forward/backward transitions
@in-duration: 150ms;
@out-duration: 100ms;
@ease-in-quad: cubic-bezier(0.55, 0.085, 0.68, 0.53);
@ease-out-quad: cubic-bezier(0.25, 0.46, 0.45, 0.94);

.cb-inverted {
  background-color: @header-bg;
  color: white;

  a {
    color: white;
    font-weight: bold;
  }
  a:hover {
    color: white;
  }

  .badge {
    background-color: white;
    color: @header-bg;
  }

  .btn-primary {
    background-color: @blue-4;
    border-color: @blue-3;
  }

  .btn-primary:hover {
    background-color: @blue-3;
    border-color: @blue-3;
  }
}

#content {
  margin: 15px 0;
  h1:first-of-type {
    font-size: 1.4rem;
    font-weight:300;
  }
}

/* for sticky footer */
html,
body {
  height: 100%;
}

// Size and align content in the header
#header {
  display: flex;
  justify-content: center;
  &.lib__margin--bottom{
    margin-bottom: 2rem;
  }
}

// Container for introductory text about a page
section.intro {
  font-size: 90%;
}

.nowrap {
  white-space: nowrap;
}

.overflow-auto {
  overflow: auto;
}

// used in dialogs showing errors, the pre doesn't need any extra margins when
// it's wrapped by a well.
.well > pre {
  margin: 0;
}

.break-all {
  word-break: break-all;
}

/* form wizard */
// .wizard-pagination class also gets styles from Bootstrap; this allows the
// markup to remain generic and agnostic about BS or other dependencies.
// Still, it does use some BS classes so it isn't perfectly generic yet.
.wizard-pagination {
  .pagination();

  > li {
    display: block;
    float: left;
    padding: 0 !important;
  }
}

.table-transparent {
  background-color: transparent !important;

  td {
    border-top-color: transparent !important;
  }
}

// Dashboard tweaks
//----------------------------------------

.dashboard {
}

[data-post="click"] {
  cursor: pointer;
}

// Special case for order form: align inputs and select widgets and allow
// them to take up their table cell's width.
#service-items {
  input:not([type="radio"]),
  select {
    width: 100%;
  }
}

// Fix bootstrap-datepicker default z-index so it's above our modals
body.modal-open .datepicker {
  z-index: 1151 !important;
}

// Dates
.date-part {
}
.time-part {
  color: #ccc;
}

// Present a dividing line that's got narrower margins and optional text label
.section-dividing-line {
  position: relative;
  border-bottom: 1px solid @table-border-color;
  margin: 2em;
  height: 0;
  text-align: center;

  span {
    color: darken(@table-border-color, 20%);
    background-color: white;
    padding: 8px;
    font-size: 16px;
    position: relative;
    // Vertically center label on line (0.5 * font-size) + padding-top
    top: -12px;
  }
}

.mirror-horizontally {
  -moz-transform: scaleX(-1);
  -o-transform: scaleX(-1);
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
  filter: FlipH;
  -ms-filter: "FlipH";
}

.text-underline {
  text-decoration: underline;
}

// Log in page
//----------------------------------------
.panel-narrow {
  width: 460px;

  .panel-body {
    padding: 2rem;
  }

  // Scale the banner image proportionately down to fit
  img.banner {
    // (panel-width) - 2*panel-border - 2*panel-padding
    max-width: 368px;
    margin-bottom: 30px;
  }

  // Keep addons and input-groups from wrapping
  label {
    display: inline;
  }

  .cb-login-label {
    font-size: 0.75rem;
    color: #999999;
  }

  .cb-login-button {
    min-width: 220px;
    text-align: center;
  }

  .cb-login-pad-top {
    padding-top: 1.25rem;
  }

  .toggle-password {
    top: -2rem;
  }
}

// Large icon for visualizing table relationships (e.g. right arrow
// in the Event Automations table)
td.icon-column {
  color: #ccc;
  font-size: 2em;
  padding: 8px 32px !important;
  text-align: center;
}

.cb-tag {
  .btn;
  .btn-xs;
  .btn-default;

  // By default tags do not appear clickable
  cursor: default;

  // Only when tags are contained in this class will they appear clickable.
  // Since tags have no inherent behavior, click handlers must be defined by the
  // page anyway, so this way of indicating it makes sense.
  .clickable-tags & {
    cursor: pointer;
  }
}

.pre {
  // Show icons in preformatted text sections (e.g. job output) inline with the text
  .icon {
    display: inline;
    padding: 0 3px;
  }
}

// common.widgets.ButtonColumns
.button-columns {
  label.btn {
    // Let buttons take up full width of column, after a bit of margin
    margin: 4px;
    width: 100%;
  }

  input[type="radio"] {
    visibility: hidden;
  }
}

ul.preconfig-options {
  .list-unstyled;

  li {
    padding-bottom: 4px;

    // Refine layout of input and its label
    input[type="radio"] {
      // Override .form-control's width of 100% which prevents labels from being adjacent
      width: auto !important;
      display: inline;
      margin-right: 0.5em;
    }
  }
}

textarea.license-text {
  font-family: monospace;
  font-size: 8pt;
}

// The following adjustments where made in May 2016 by
// Nathan Gustafson, nathan@twentyideas.com

//
// Page Headings

.uplink {
  background: none;
  font-weight: 100;
  margin-right: -8px;
  margin-bottom: 16px;
}
.uplink,
.uplink a {
  color: #2f54aa;
}

.uplink::after {
  content: "/";
  color: #bbb;
  margin-left: 15px;
}
.uplink + h1 {
  display: inline-block;
  font-weight: 600;
}

// Breadcrumb text looks too big at H1's 36px, so decrease it
// and give uplink the same font size.

.uplink,
.uplink + h1 {
  font-size: 1.4rem;
  font-weight: 300;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-weight: 300;
  color: #444;
}
h1,
.h1 {
  font-weight: 100;
}

//
// Styles to adjust font weight of header navigation
// and bold just the active link

.nav-header-tabs {
  font-weight: 100;
}

// Remove highlighted tab; bold is enough
.nav-header-tabs > li.active > a,
.nav-header-tabs > li.active > a:focus {
  background-color: transparent !important;
  font-weight: bold;
}

.nav > li > a {
  font-weight: 100;
}

// More subtle borders between tabs
.nav-boxed-tabs > li > a {
  border-left-color: #e3e3e3;
}
// Except the first tab (don't know which LESS variable so hard-coding)
.nav-boxed-tabs > li:first-child > a,
// Except the active tab's right border
.nav-boxed-tabs > li.active + li > a {
  border-left-color: #ccc;
}

.nav-boxed-tabs > li.active > a,
.nav-boxed-tabs > li.active > a:hover,
.nav-boxed-tabs > li.active > a:focus {
  font-weight: bold;
}

// Prevent document from being wider than window. This causes a horizontal
// scroll bar to appear, and scrolling leads to an unstyled empty area which is
// poor UX.  The wide Bookmarks submenu, even when not visible, may cause this.
.vbox {
  overflow: hidden;
}

// Content Library

table.content-from-content-library {
  tbody {
    tr {
      opacity: 0.5;
    }
    tr:hover {
      opacity: 1;
    }

    td {
      .btn {
        width: 100%;
      }
      .import-link {
        width: auto;
        text-align: center;
      }
    }
  }
}

.state-error {
  color: darken(@alert-danger-bg, 50%);
}

.state-ready {
  color: darken(@alert-success-bg, 50%);
}

.state-warning {
  color: darken(@alert-warning-bg, 50%);
}

.btn-toolbar .btn,
.btn-toolbar .btn-group,
.btn-toolbar .input-group {
  float: none;
}

//
// Ensure drop-down menus have visible titles
// [DEV-17313]
//
.dropdown-menu * .panel-title {
  color: #333333;
}

.pagination-container {
  .pagination {
    .page-item {
      margin: 0 0.2rem;
      width: 2rem;
      height: 2rem;
      .page-link {
        border: none;
        width: 100%;
        height: 100%;
        border-radius: 5px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      button.page-link {
        background-color: #ffffff !important;
        border: 1px solid #bfbfbf;
      }
      &.active {
        button.page-link {
          background-color: #5d6a73 !important;
          color: #ffffff !important;
        }
      }
      &.disabled {
        .page-link {
          background-color: transparent !important;
          cursor: not-allowed;
        }
      }
    }
  }
}
