// BS defines mixins for transition{,-property,-delay,-duration} but not
// transition-timing-function. We add that here.

.transition-timing-function(@timing-function) {
    -webkit-transition-timing-function: @timing-function;
            transition-timing-function: @timing-function;
}


// use shadows to indicate when items exist outside of the scrollable area's viewport
// Technique from: https://gist.github.com/LeaVerou/2462915
.overflow-shadow() {
  @cover-height: 2rem;
  @shadow-height: 0.5rem;
  overflow: auto;
  background:
    /* Shadow covers */
    linear-gradient(white 25%, rgba(255,255,255,0)),
    linear-gradient(rgba(255,255,255,0), white 75%) 0 100%,
    /* Shadows */
    radial-gradient(farthest-side at 50% 0, rgba(0,0,0,.4), rgba(0,0,0,0)),
    radial-gradient(farthest-side at 50% 100%, rgba(0,0,0,.4), rgba(0,0,0,0)) 0 100%;
  background-size:
    100% @cover-height,
    100% @cover-height,
    100% @shadow-height,
    100% @shadow-height;
  background-attachment:
    local,
    local,
    scroll,
    scroll;
  background-repeat: no-repeat;
}

// make it available as a class
.overflow-shadow {
  .overflow-shadow();
}

// mixin to properly display list items (and potentially other elements)
// in areas where user-input markdown renders (e.g., blueprint descriptions)
.contains-md() {
  li {
    list-style-type: inherit !important;
  }
}

// make available as a class
.contains-md {
  .contains-md();
}

/* Special purpose widget provides a fixed-height bordered box containing a
 * scrollable table.
 * Removes
 */
.scrollable-table-container {
  // frame the scroll area
  border: 1px solid @table-border-color;
  border-radius: @panel-border-radius;

  // hint to users that they can scroll up or down for more content
  .overflow-shadow();

  table {
    // remove the default 20px bottom margin from Bootstrap's .table
    .no-margin;

    // darker heading and 1px border instead of default 2px
    thead tr th {
      background-color: darken(@panel-bg, 3%);
      border-bottom-width: 1px;
    }
  }
}

.transform(...) {
  -webkit-transform: @arguments;
     -moz-transform: @arguments;
      -ms-transform: @arguments;
          transform: @arguments;
}

.transform-origin(@origin) {
  -webkit-transform-origin: @origin;
     -moz-transform-origin: @origin;
      -ms-transform-origin: @origin; // IE9 only
          transform-origin: @origin;
}

.transition-timing-function(@timing-function) {
  -webkit-transition-timing-function: @timing-function;
          transition-timing-function: @timing-function;
}
