/*
 * Dialogs
 *
 * Most styles for our modal come from Bootstrap 3.
 */

.modal {
  &.fade {
    & .modal-dialog {
      transform: translateY(0) !important;
      min-width: 650px !important;
    }
    &.in,
    &.show {
      //correct class is .show, left .in for back compat
      background: rgba(0, 0, 0, 0.5) !important;
      opacity: 1 !important;
    }
  }
}

.modal-full {
  width: calc(100vw - 6rem);
}

.modal-backdrop {
  background: rgba(0, 0, 0, 0.5) !important;
}

.danger .modal-content {
  border: solid @brand-danger 4px;
}
.modal-content {
  .modal-body {
    min-height: 100%;
    height: 100%;
    display: block;
    form {
      border: none;
    }
    p {
      font-size: 14px;
      font-family: open_sans, "Helvetica Neue", Helvetica, Arial, sans-serif;
    }
  }
}

.modal-header {
  // make left and right .modal-header padding match the padding
  // given to .modal-body. This ensures a strong left line.
  margin: 0 1rem;
  padding: @modal-title-padding 0;
  display: flex;
  align-items: center !important;
  .modal-title {
    width: 97%;
    font-size: 18px;
    font-weight: 400;
    font-family: open_sans, "Helvetica Neue", Helvetica, Arial, sans-serif;
  }
  .close {
    margin: 0;
    opacity: 0.2 !important;
    &:hover {
      opacity: 0.5 !important;
    }
  }
}

.modal-footer {
  // remove BS's margin-top because of personal preference
  margin: 0 1rem;
  display: flex;
  justify-content: flex-end;
  .cb-btn {
    margin-left: 0.75rem;
    min-width: 6rem;
    max-width: 8rem;
  }
  .btn-toolbar {
    display: flex;
    flex-basis: 100%;
    justify-content: flex-end;
    align-items: center;
  }
}

// Don't let users resize textareas off the side of the modal
.modal-dialog textarea {
  resize: vertical;
}
