/*
 * These IE10+ CSS styles will only apply when Windows High Contrast Mode is on.
 */

@media screen and (-ms-high-contrast: active) {
    /* mode is not black-on-white or white-on-black */
}


@media screen and (-ms-high-contrast: black-on-white) {
    ul.nav li[data-topnav="dashboard"] {
        a {
            /* Fix default CB logo which is white and disappears otherwise */
            filter: invert(100%) !important;
            /* hide the space char which is now probably visible */
            text-indent: -2000px;
        }

        /* Undo our hover and active bg colors for CB logo (or customer's) by letting IE's high
         * contrast bg color through so it is correctly inverted by our filter above. That is,
         * keep the logo from disappearing by forcing it not to change.
         */
        a:hover,
        &.active a {
            background-color: inherit !important;
        }
    }

    /* Chart colors should honor chosen color scheme */
    .highcharts-container svg text,
    .highcharts-container svg rect                       { fill: black !important; }
    .highcharts-container svg rect.highcharts-background { fill: white !important; }
    /* Line charts: wider lines contrast bg, but keep color on the marker dots so they match the legend */
    .highcharts-container svg .highcharts-series path    { stroke: black !important; stroke-width: 3; }
}


@media screen and (-ms-high-contrast: white-on-black) {
    /* Chart colors should honor chosen color scheme */
    .highcharts-container svg text,
    .highcharts-container svg rect                       { fill: white !important; }
    .highcharts-container svg rect.highcharts-background { fill: black !important; }
    /* Line charts: wider lines contrast bg, but keep color on the marker dots so they match the legend */
    .highcharts-container svg .highcharts-series path    { stroke: white !important; stroke-width: 3; }
}
