// Overrides for the default rules from seiyria-bootstrap-slider

// Load LESS straight from the 3rd party project
@import "../node_modules/bootstrap-slider/src/less/variables.less";
@import "../node_modules/bootstrap-slider/src/less/rules.less";

// Override as needed.

@slider-line-height: @line-height-computed;
@slider-handle-width: 0.6 * @slider-line-height;
@slider-handle-height: 1.3 * @slider-line-height;


.slider.slider-horizontal {

  // Default is hardcoded 210px which is unnecessarily narrow for us.
  // The rest of the width is expected to be taken up by the .slider-value
  // span.  Wide values (e.g. 20000) may cause trouble here.
  width: 80%;

  // Indent slightly so handles line up with other form fields on left
  margin: 0 (@slider-handle-width / 2);

  &.slider-horizontal {
    .slider-track {

      // Track to the left of the handle
      .slider-selection {
        #gradient > .vertical(@blue-6, darken(@blue-6, 20%));
      }

    }

    .slider-tick {
    }

    .slider-handle {

      &.custom {
        .btn;
        .btn-default;

        width: @slider-handle-width;
        height: @slider-handle-height;

        margin-top: (-@slider-handle-height / 2) + (@slider-line-height / 4);
        margin-left: (-@slider-handle-width / 2);
        padding: 0;

        // Remove default star character
        &::before{ content: ''; }
      }
    }
  }
}


.slider-value {
  vertical-align: middle;
  margin-left: 0.5em;
}
