/*
 * Miscellaneous widgets
 */


// Fix the bootstrap-colorpicker widget positioning
.colorpicker-container {
  position: relative;
}

.modal-dialog .colorpicker-modal-fix {
  left: auto;
  right: 0;
}

.toggle-password {
  float: right;
  margin-left: -26px;
  margin-top: -26px;
  padding: 0 10px 0 0;
  position: relative;
  z-index: 2;
  opacity: 0.5;
  filter: alpha(opacity=50); /* For IE8 and earlier */
}

.toggle-password:hover {
  opacity: 0.8;
  filter: alpha(opacity=80); /* For IE8 and earlier */
}

// Selectize style customizations and fixes
// --------------------------------------------------


.selectize-input {
  &.dropdown-active {
    .selectize-border-radius(@selectize-border-radius);

    /* In Chrome:
     * When a selectize has a selection, activating the dropdown pushes
     * the following content down 1px.
     *
     * Seems due to the positioning of the input element it contains
     * changing from absolute to relative, or maybe the pseudo elements.
     *
     * Setting padding-bottom: 0 here and in the next rule "fixes" it
     * but I don't understand why.  Actually, the shift still occurs the
     * first time a selection is made, but then everything stops shifting.
     */
    padding-bottom: 6px;
  }


  // 'full' means there is at least one selection: prevent the 1px shift.
  &.dropdown-active.full {
    padding-bottom: 5px !important;
  }

  // Override our base style for input[type=text] for consistent height.
  // Doing this has one side effect: the 1px shift described above.
  input {
    height: auto;
  }

}


// Tag Checkbox Widget
// --------------------------------------------------

span.tag-checkbox-spacer {
  display: inline-block;
  width: 1em;
  height: 1em;
  border-left: 1px solid #ddd;
  transform: scaley(2) translate(0.4em);
}