/* A companion to Bootstrap's `.container`, `.full-container` is full-width and
 * takes no rows or columns. */
.full-container {
    padding: 1em;
}
.full-container-no-top {
    padding-top: 0;
}
.full-container-no-bottom {
    padding-bottom: 0;
}

.fluid-container {
  .container-fixed();
  // We can provide sensible colums sizes up to the large media query, but not
  // beyond. Setting a max width prevents strangely-wide displays from having
  // content that's too wide.
  max-width: 110rem; // a bit over 1920px for Chris

  // Wider container padding than the Bootstrap default 15px
  @media (min-width: @screen-md-min) { padding-left: 30px; padding-right: 30px; }
  @media (min-width: @screen-lg-min) { padding-left: 45px; padding-right: 45px; }
}


.vbox {
    display: table;
    // Though `table-layout: fixed` is not necessary for the .vbox layout, for
    // content in Firefox that uses `max-width: 100%` on an element inside a
    // `display: table` element, the fixed layout can make the content render
    // more like other browsers do. https://bugzilla.mozilla.org/show_bug.cgi?id=975632
    // A concrete benefit is permitting the sane usage of `.img-responsive` on
    // an element within this layout.
    table-layout: fixed;

    width: 100%;
    height: 100%;

    > .box-shrink {
        display: table-row;
        // `0` yields surprising results in Firefox, so we use `1px`.
        height: 1px;
    }

    > .box-expand {
        display: table-row;
        height: auto;
    }
}

.dead-center-container {
    display: table;
    width: 100%;
    height: 100%;
}
.dead-center-content {
    display: table-cell;
    vertical-align: middle;
    text-align: center;

    > * {
      text-align: left;
      display: inline-block;
    }
}


/* Vertical centering of content
 *
 * Usage:
 *     div.center-vertically <- give this a height
 *         div.center-vertically-inner
 *             img.example
 *
 * Used in large blueprint and resource cards to center logo and name
 */
.center-vertically {
  display: table;
  width: 100%; // expand to fit container

  > .center-vertically-inner {
    display: table-cell;
    vertical-align: middle;
  }
}


/*
 * General block level styles
 */

// Standard way to add extra space below something, like a p
.padded-under {
  padding-bottom: 2em;
}

.padding {
  padding: 8px;
}

.margin {
  margin: 8px;
}
