.popover.tour {
  &.max-width-400 {
    max-width: 400px !important;
  }
  .arrow.arrow-fix {
    top: 25px !important;
  }
  .popover-title {
    font-weight: bold;
    font-size: 16px !important;
  }
  .popover-content a {
    color: #0d6aad;
  }
  .popover-navigation.btn-fix button {
    line-height: 1.5;
    font-size: 14px;
    padding: 6px 12px;
  }
}
