/* get rid of top margin when using headers in other contexts */
h1, h2, h3, h4, h5, h6 {
  &:first-child {
    /* We use heading tags in tables and lists where margins
     * on these would usually break layout or alignment */
    margin-top: 0;
  }
}

h1, h2, h3 {
    /* less top margin than BS default */
    margin-top: (@line-height-computed / 2);
}
