.no-margin {
  margin: 0;
}

.no-padding {
  padding: 0 !important;
}

// columns prefixes
.columns(@args) {
  -webkit-columns: @args;
     -moz-columns: @args;
          columns: @args;
}
.column-count(@count) {
  -webkit-column-count: @count;
     -moz-column-count: @count;
          column-count: @count;
}
.column-gap(@width) {
  -webkit-column-gap: @width;
     -moz-column-gap: @width;
          column-gap: @width;
}
.column-width(@width) {
  -webkit-column-width: @width;
     -moz-column-width: @width;
          column-width: @width;
}
.break-inside(@value) {
  -webkit-column-break-inside: @value;
            page-break-inside: @value; // Firefox https://bugzilla.mozilla.org/show_bug.cgi?id=549114#c10
                 break-inside: @value;
}

// Simple reusable show-on-hover behavior
.revealer .revealable {
  opacity: 0.0;
  .transition-property(opacity;);
  .transition-duration(200ms);
  .transition-timing-function(ease);
}
.revealer:hover .revealable {
  opacity: 1.0;
}
