#dashboard-report-list {

    ul {
        columns: 15em;
        padding-left: 0;

        li {
            break-inside: avoid;

            a.btn {
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;

                img.icon {
                    width: 24px !important;
                }

            }
            a.btn:hover {
                background-color: @btn-default-bg;
            }
        }
    }
}

.dashboard {
    #blueprints {
        .card__descript {
            .contains-md();
        }
    }
}
