// Larger/taller progress bar with good sized text (e.g. job details)
.progress-time {
  color: #CCC;
}

.progress.with-times {
  margin-bottom: 0 !important;
}

.progress-lg {
  @bar-height: @line-height-computed * 1.5;

  height: @bar-height !important;

  .progress-bar {
    font-size: @font-size-base * 1.125;
    line-height: @bar-height;

    // The duration text inside the progress bar needs to work on an empty bar
    // as well as the various colors, so effectively gets an outline via text
    // shadow.
    span {
      color: #fff;
      text-shadow: 0 0 2px #000;
      padding: 0 1em;
    }
  }
}
